/*
*
* Main Styles
* ==================================================
*/

// Remove leading spacing of element
html {
  *:first-child {
    margin-top: 0;
  }

  *:last-child {
    margin-bottom: 0;
  }
}

html.boxed{
  body{
    background: url('../images/mooning.png') repeat;
    //background: linear-gradient(45deg, #151515 12%, transparent 0, transparent 88%, #151515 0),
    //linear-gradient(135deg, transparent 37%, #181818 0, #181818 63%, transparent 0),
    //linear-gradient(45deg, transparent 37%, #151515 0, #151515 63%, transparent 0) #131313;
    //background-size: 35px 35px;
  }

  .page{
    max-width: 1600px;
    margin-left: auto;
    margin-right: auto;
  }

  .rd-navbar.rd-navbar--is-stuck{
    max-width: 1600px;
    margin-left: auto;
    margin-right: auto;
  }

  .rd-navbar-static ,
  .rd-navbar-fullwidth  {
    .rd-navbar-inner {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  @media (min-width: $screen-xxl-min) {
    .rd-navbar-static .rd-navbar-nav > li {
      margin: 0 22px;
    }
    .rd-navbar-corporate.rd-navbar-static .rd-navbar-nav > li {
      margin: 0;
    }
    .page-footer-default-var-2 .rd-google-map-wrap {
      width: calc(100vw / 2.5);
    }
  }

  &.contrast{
    body{
      background: linear-gradient(45deg, #f3f3f3 12%, transparent 0, transparent 88%, #f3f3f3 0),
      linear-gradient(135deg, transparent 37%, #f1f1f1 0, #f1f1f1 63%, transparent 0),
      linear-gradient(45deg, transparent 37%, #f3f3f3 0, #f3f3f3 63%, transparent 0) #f9f9f9;
      background-size: 35px 35px;
    }
  }
}



// Body

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  color: $text-color;
  background-color: $body-bg;
}


// Links

a {
  color: $link-color;
  text-decoration: none;
  transition: 270ms ease-in;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    outline: none;
  }

  &[href*='callto'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

a.link-primary {
  color: $link-color;
  &:hover{
    color: $link-hover-color;
  }
}

.link-hover{
  color: $link-hover-color;
}

.link-press{
  color: $link-press-color;
}

.p a:hover,
.list a:hover{
  text-decoration: underline;
}
p {
  margin: 0;
}

mark {
  background-color: $primary;
  color: $white;
  padding-bottom: 5px;
}


// Images

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}


/*
*
* Typography
* ==================================================
*/

#{headings()} {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  color: $headings-color;

  a {
    display: inline;
    color: inherit;
    transition: .3s all ease;

    &:hover {
      color: $primary;
    }
  }
}

// {0 - 768px} <= 40px
// {768px - 992px} <= 62px
h1,
.heading-1 {
  font-size: 34px;
  line-height: 1.2;
  @media (min-width: $screen-sm-min) {
    font-size: 42px;
    line-height: $font-lh-h1;
  }
  @media (min-width: $screen-lg-min) {
    font-size: 60px;
  }
  @media (min-width: $screen-xl-min) {
    font-size: $font-size-h1;
  }
}

// {0 - 768px} <= 36px
// {768px - 992px} <= 52px
h2,
.heading-2 {
  font-size: 28px;
  line-height: 1.2;

  @media (min-width: $screen-sm-min) {
    font-size: 34px;
    line-height: $font-lh-h2;
  }
  @media (min-width: $screen-lg-min) {
    font-size: 36px;
  }
  @media (min-width: $screen-xl-min) {
    font-size: $font-size-h2;
  }
}

// {0 - 768px} <= 32px
// {768px - 992px} <= 44px
h3,
.heading-3 {
  font-size: 24px;
  line-height: 1.45;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h3;
    line-height: $font-lh-h3;
  }
}

// {0 - 768px} <= 26px
// {768px - 992px} <= 36px
h4,
.heading-4 {
  font-size: 19px;
  line-height: 1.35;
  font-weight: 400;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h4;
    line-height: $font-lh-h4;
  }
}

// {0 - 768px} <= 22px
// {768px - 992px} <= 28px
h5,
.heading-5 {
  font-weight: 400;
  font-size: 17px;
  line-height: 1.5;
  font-style: italic;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h5;
  }
  @media (min-width: $screen-xl-min) {
    font-size: $font-size-h5;
  }
}

p.heading-5 {
  color: $text-color;
}

// {0 - 768px} <= 18px
// {768px - 992px} <= 22px
h6,
.heading-6 {
  font-family: $font-family-base;
  font-size: 15px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h6;
    line-height: $font-lh-h6;
  }
}

// Big text
.big {
  font-size: $font-size-large;
}


// Small text
small,
.small {
  display: block;
  font-size: $font-size-small;
}


// Divider
// -------------------------

hr{
  margin-top: 0;
  margin-bottom: 0;
  border-top: 1px solid $gray-4;
}

.divider {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 0;
  border: none;

  height: 4px;
  width: 60px;
  background-color: $gray-base;
  
  &-secondary {
    background-color: $secondary;
  }

  &-primary {
    background-color: $primary;
  }
  
  &-xs {
    height: 2px;
    width: 30px;
  }
}


/*
*
* Backgrounds
* ==================================================
*/
// bg-behaviour() off padding top if two section with same bg that are beside

.bg-default {
  @include bg-behaviour($body-bg);
}

.bg-white {
  @include bg-behaviour($white);
}

.bg-gray-lighter {
  @include bg-behaviour($gray-lighter);

  .form-input,
  .select2-container .select2-choice { 
    background-color: $white;
  }
  
}

.bg-primary {
  @include bg-behaviour($primary);
  @extend %context-dark;

  a.thumbnail-classic-title {
    color: $secondary;
    &:hover{
      color: $white;   
    }
  }

  // Change color of primary button on primary bg
  .btn-primary {
    @include btn-variant($primary, $white, $white, $primary, darken($white, 5%), darken($white, 5%));
  }
}

.bg-secondary {
  @include bg-behaviour($secondary);
  @extend %context-dark;

  a.thumbnail-classic-title {
    color: $orange;

    &:hover{
      color: $white;
    }
  }

  .divider-secondary,
  .divider-primary { background: $orange; }

  // Change color of primary button on primary bg
  .btn-primary {
    @include btn-variant($primary, $white, $white, $primary, darken($white, 5%), darken($white, 5%));
  }
}

.bg-gray-darker {
  @include bg-behaviour($gray-darker);
  @extend %context-dark;
}

.bg-blue-dark {
  @include bg-behaviour($blue-dark);
  @extend %context-dark;
}

.bg-image {
  background-size: cover;
  background-position: center center;
}

html.desktop {
  .bg-fixed {
    @media (min-width: $screen-md-min) {
      background-attachment: fixed;
    }
  }
}


/*
*
* Colors
* ==================================================
*/

.page {
  @include text-variant('.text-gray-base', $gray-base, $primary);
  @include text-variant('.text-gray-darker', $gray-darker, $primary);
  @include text-variant('.text-gray-dark', $gray-dark, $primary);
  @include text-variant('.text-gray', $gray, $primary);
  @include text-variant('.text-gray-light', $gray-light, $primary);
  @include text-variant('.text-gray-lighter', $gray-lighter, $primary);
  @include text-variant('.text-primary', $primary, darken($primary, 10%));
  @include text-variant('.text-white', $white, $primary);
}


/*
*
* Main layout
* ==================================================
*/

// Page
//

.page {
  position: relative;
  overflow: hidden;
  min-height: 100vh!important;
}


// Page header
//

.page-header {
  //Kill bootstrap style
  padding: 0;
  margin: 0;
  border-bottom: none;
}


// Page footer
//

.page-footer {
  color: $white;
  background-color: $blue-dark;
  overflow: hidden;

  h1, .heading-1, h2, .heading-2, h3, .heading-3, h4, .heading-4, h5, .heading-5, h6, .heading-6 {
    color: $white;
  }

  h4 + * {
    margin-top: 23px;
  }

  a {
    color: $gray-1;
    &:hover{
      color: $white;
    }
  }

  .rights {
    a {
      color: $white;

      &:hover{
        color: $primary;
      }
    }
  }
  .logo-default {
    display: none;
  }
  .logo-inverse {
    display: inline-block;
  }
}


figure {
  figcaption {
    margin-top: 20px;
  }
}

@media (min-width: $screen-sm-min) {
  .figure-layout_mod-1 {
    img {
      max-width: 280px;  
    }
  }
  .figure-layout {
    &-offset-top-3 {
      html & {
        margin-top: 10px;
      }
    }
    &-offset-top-4 {
      html & {
        margin-top: -30px;
      }
    }
  }
}

@media (min-width: $screen-md-min) {
  .figure-layout_mod-1 {
    img {
      max-width: 480px;
    }
  }
}

@media (min-width: $screen-lg-min) {
  .figure-layout {
    display: inline-block;
    img {
      max-width: none;
    }

    &-offset-top-1 {
      html & {
        margin-top: 145px;
      }
    }
    &-offset-top-2 {
      html & {
        margin-top: 80px;
      }
    }
    &-offset-top-3 {
      html & {
        margin-top: 35px;
      }
    }
    &-offset-top-4 {
      html & {
        margin-top: -80px;
      }
    }
  }
  .figure-layout_mod-1 {
    img {
      max-width: 480px;
    }
  }
}

@media (min-width: $screen-xl-min) {
  .figure-layout_mod-1 {
    img {
      max-width: none;
    }
  }
}

.figure-light {
  figcaption {
    color: $gray-light;
    font-style: italic;
  }
}

.subtitle {
  color: $primary;
  font-family: $headings-font-family;
  font-style: italic;

  @media (min-width: $screen-lg-min) {
    font-size: 18px;
  }
}

.range-bordered {
  > [class*='cell'] {
    &:nth-child(n+2) {
      border-top: 1px solid rgba($gray-base, .3);
    }
  }

  @media (min-width: $screen-xs-min) {
    > [class*='cell'] {
      &:nth-child(2) {
        border-top: 0;
      }
      &:nth-child(2n) {
        border-left: 1px solid rgba($gray-base, .3);
      }
    }
  }
  @media (min-width: $screen-md-min) {
    > [class*='cell'] {
      border-top: 0; 
      &:nth-child(n + 2) {
        border-top: 0;
        border-left: 1px solid rgba($gray-base, .3);
      }
    }
  }
}

// text block

.text-block {
  @include group(30px);
  @media (min-width: $screen-md-min) {
    figure {
      float: left;
      max-width: 40%;
      margin-right: 30px;
      padding-bottom: 15px;
    }
    &-inner {
      display: block;
    }
  }
  @media (min-width: $screen-xl-min) {
    figure {
      max-width: 100%;
    }
    &-inner {
      overflow: hidden;
    }
  }
}

.question {
  font-size: 18px;
  color: $gray-darker;
}
.question + .answer { margin-top: 10px; }

.inset-horizontal-15 {
  padding-left: 15px;
  padding-right: 15px;

  @media (min-width: $screen-lg-min) {
    padding-left: 0;
    padding-right: 0;
  }
}

.custom-img-styles {
  .range { min-height: 100% }

  > .range {
    margin-left: -5px;
    margin-right: -5px;
  }

  img { width: 100%; }

  @media (min-width: $screen-lg-min) {
    > .range {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .section-wrap-image {
    position: relative;
    overflow: hidden;

    @media (min-width: $screen-lg-min) {
      img {
        position: absolute;
        height: auto;
        min-width: 100%;
        min-height: 100%;
        max-width: none;
        top: 30%;
        left: 50%;
        transform: translate(-50%, -30%);
      }

      @supports (object-fit: cover) {
        img {
          top: 0;
          left: 0;
          transform: none;
          height: 100%;
          width: 100%;
          object-fit: cover;
          object-position: center center;
        }
      }
    }
  }
}

.ie-10,
.ie-11,
.ie-edge {
  .custom-img-styles {
    .range {
      height: 100%
    }
  }
}