/*
*
* Responsive unit
* --------------------------------------------------
*/


@include unit-responsive($medias, 15px, 30px);

// Unit Spacing
//

.unit-spacing-xs {
  @include unit-spacing($medias, 7px, 15px);
}

.unit-spacing-sm {
  @include unit-spacing($medias, 25px, 35px);
}

.unit-spacing-md {
  @include unit-spacing($medias, 25px, 50px);
}