/*
*
* Contexts
* --------------------------------------------------
*/

%context-dark {
  h1, .heading-1, h2, .heading-2, h3, .heading-3, h4, .heading-4, h5, .heading-5, h6, .heading-6, & {
    color: $white;
  }
  & {
    //color: $text-color-contrast;
  }

  .subtitle {
    color: rgba($white, .7);
  }
  
  // Quote modern
  .quote-modern-text::before,
  .quote-modern-cite {
    color: $orange;
  }
  .quote-modern-small {
    color: rgba($white, .7);
  }
  
  .owl-dot {
    span {
      border: 2px solid $white;
      &:after {
        background: $white;
        box-shadow: 0 0 1px $white;
      }
    }
    &.active,
    &:hover,
    &:focus {
      span {
        border-color: $white;
      } 
    }
  }

  .time_circles > div > span {
    color: $white;
  }

  .progress-linear {
    .progress-bar-linear {
      background-image: linear-gradient(to right, $secondary 60%, darken($secondary, 10.8%));
    }
    .progress-header {
      color: $white;
    }
  }
  .counter {
    color: $secondary;

    &-wrap{
      color: $white;
    }
  }

  .single-post-body-inner {
    color: $gray-4;
  }
  .list-contact a {
    color: $gray-4;
    &:hover{
      color: $primary;
    }
  }
  .single-post-title {
    color: $primary;
  }
  .services-box-text {
    opacity: .7;
  }

  .range-bordered {
    > [class*='cell'] {
      & + [class*='cell'] {
        border-color: rgba($white, .3);
      }
    }
  }
} 

%context-light {
  h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
    color: $headings-color;
  }

  & {
    color: $text-color;
  }
}

// Context Invariable
%context-dark-invariable {
  h1, .heading-1, h2, .heading-2, h3, .heading-3, h4, .heading-4, h5, .heading-5, h6, .heading-6, & {
    color: #fff;
  }
}

.context-dark {
  @extend %context-dark;
}

.context-light {
  @extend %context-light;
}

.context-dark-invariable {
  @extend %context-dark-invariable;
}


