// ================
// Pricing
// ================

.pricing-table-carousel {
  .owl-stage-outer {
    padding-top: 40px;
    padding-bottom: 20px;
  }
  @media (min-width: $screen-lg-min) {
    .owl-controls {
      display: none;
    }
  }
  @media (min-width: $screen-xl-min) {
    .owl-stage-outer {
      padding-top: 60px;
    }
  }
}

.pricing-table {
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;

  &-active {
    position: relative;
    max-width: 420px;
    padding: 20px;
    transform: translateY(-20px);
    box-shadow: $shadow-area-sm;
    
    &:after {
      position: absolute;
      top: -17px;
      right: 20px;
      width: 70px;
      height: 70px;
      display: flex;
      align-items: center;
      border-radius: 50%;
      background-color: $primary;
      color: $white;
      font-family: $font-family-sec;
      font-size: 14px;
      line-height: 1.2;
      content: 'Most Popular';
      transform: rotate(-30deg);
    }
  }
}

.pricing-table-title {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid $primary;
  h3 {
    font-weight: 400;
  }
}

.pricing-table-body {
  padding-top: 15px;
  padding-bottom: 15px;
  ul {
    li {
      padding-top: 10px;
      padding-bottom: 10px;
      color: $gray-4;

      &.active {
        color: $gray-darker;
      }
    }
  }
}

.pricing-table-price {
  > span {
    display: inline-block;
    color: $primary;
    font-weight: 400;
    &:before {
      content: "$";
      display: inline-block;
      vertical-align: super;
      font-size: 24px;
    }
  }
}

.pricing-table-footer {
  padding-top: 10px;
  padding-bottom: 10px;
}

@media (min-width: $screen-sm-min) {
  .pricing-table-title {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .pricing-table-body {
    padding-top: 25px;
    padding-bottom: 25px;
  }
  .pricing-table-footer {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

@media (min-width: $screen-xl-min) {
  .pricing-table-active {
    padding: 33px 60px 55px;
    transform: translateY(-33px);
    &:after {
      position: absolute;
      top: -17px;
      right: 20px;
      width: 100px;
      height: 100px;
      font-size: 18px;
    }
  }
}