/*
*
* Progress Bars
* --------------------------------------------------
*/



// Linear Progress Bars
// --------------------------------------------------
.progress-container {
  max-width: 570px;
  margin-left: auto;
  margin-right: auto;
} 
.progress-linear {
  position: relative;
  text-align: left;

  .progress-header{
    @include display-flex();
    @include align-items(center);
    @include justify-content(space-between);
    @include group(10px);

    font-size: 18px;
    color: $gray-darker;
  }

  .progress-bar-linear-wrap, .progress-bar-linear {
    height: 100%;
    border-radius: 0;
  }

  .progress-bar-linear-wrap {
   background: $gray-lighter;
    height: 12px;
    margin-top: 8px;
  }

  .progress-bar-linear {
    background-image: linear-gradient(to right, $primary 60%, darken($primary, 10.8%));
    width: 0; 
    transition: .5s all ease-in-out;
  }

  .progress-value{
    font-weight: 900;
    &:after{
      content:"%";
    }
  }
}

.progress-linear  + .progress-linear {
  margin-top: 30px;
}
