/*
*
* Offsets
* --------------------------------------------------
*/

// Element offset
h2 + * { margin-top: 25px; }
* + p { margin-top: 15px; }
* + hr { margin-top: 10px; }
* + ul { margin-top: 15px; }
p + p { margin-top: 15px; }


// Headings
h1 + h2 { margin-top: 32px; }
h2 + h3 { margin-top: 32px; }
h3 + h4 { margin-top: 32px; }
h4 + h5 { margin-top: 30px; }
h5 + h6 { margin-top: 30px; }
h1 + p { margin-top: 22px; }
h4 + h4 { margin-top: 30px; }


// Headings + Paragraph
h2 + p { margin-top: 20px; }
//h3 + p { margin-top: 12px; }
//h4 + p { margin-top: 12px; }
//h5 + p { margin-top: 12px; }
//h6 + p { margin-top: 12px; }


// Paragraph + Headings
p + h2 { margin-top: 45px; }
p + h3 { margin-top: 45px; }
p + h4 { margin-top: 45px; }
p + h5 { margin-top: 45px; }
p + h6 { margin-top: 45px; }


// Tags + Tags
img + p { margin-top: 15px; }
h3 + img { margin-top: 42px; }


// Tags + Classes
p + .list-marked { margin-top: 20px; }
.list-marked + h4 { margin-top: 30px; }
p + .list-ordered { margin-top: 20px; }
p + .range { margin-top: 40px; }
h2 + .heading-4 { margin-top: 20px; }
hr + .heading-5 { margin-top: 20px; }
h2 + .divider { margin-top: 20px; }
h4 + .list-contact { margin-top: 20px; }


// Classes
.subtitle + * { margin-top: 5px; }
* + .rd-mailform { margin-top: 30px; }
* + .group-xl { margin-top: 30px; }
* + .group-xxl { margin-top: 20px; }
* + .mailchimp-mailform { margin-top: 30px; }
* + .campaign-mailform { margin-top: 30px; }
* + .table-custom-responsive { margin-top: 30px; } 
* + .button { margin-top: 30px; } 
* + .list-contact { margin-top: 30px; } 
* + .progress-linear { margin-top: 15px; }
* + .list-marked { margin-top: 15px; }
* + .heading-5 { margin-top: 25px; }
* + .question-item { margin-top: 60px; }
* + .offset-top-30 { margin-top: 30px; }

// Classes + Tags
.range + h3 { margin-top: 80px; }


// Classes + Classes
.shell + .shell { margin-top: 60px; }
.range + .range { margin-top: 60px; }


// typography offsets
.section-typography-body {
  h1 + * {
    margin-top: 20px; 
  }
  h2 + * {
    margin-top: 20px;
  }
  h5 + * {
    margin-top: 12px;
  }
}

// Media offsets

@media (min-width: $screen-sm-min) {
  // typography offsets
  .section-typography-body {
    h1 + * {
      margin-top: 30px;
    }
    h2 + * {
      margin-top: 30px;
    }
    h5 + * {
      margin-top: 20px;
    }
  }

  // Element offset
  h2 + * { margin-top: 35px; }
  h2 + .heading-4 { margin-top: 30px; }
  .table-custom-responsive + p { margin-top: 35px; }

  * + .progress-linear { margin-top: 30px; }

  .owl-carousel + .button { margin-top: 60px; }
}

@media (min-width: $screen-md-min) {
  figure + p { margin-top: 30px; }
  h2 + p { margin-top: 30px; }
  p + p { margin-top: 20px; }
  h4 + h4 { margin-top: 60px; }
  
  h2 + .heading-4 { margin-top: 40px; }
  hr + .heading-5 { margin-top: 40px; }
  p + .range { margin-top: 60px; }
  .list-marked + h4 { margin-top: 60px; }
}

@media (min-width: $screen-lg-min) {
  .range + .button {
    margin-top: 60px;
  }
}

// inset
.inset-left-70 {
  
}

@media (min-width: $screen-lg-min) {
  .inset-left-70 {
    padding-right: 15px;
  }
}

@media (min-width: $screen-xl-min) {
  .inset-left-70 {
    padding-right: 70px;
  }
}

html {
  // Range spacing
  .range-60 {
    @include responsive-offset(50px);
  }
  
  .range-55 {
    @include responsive-offset(40px);
  }
  
  .range-50 {
    @include responsive-offset(40px);
  }

  .range-40 {
    @include responsive-offset(40px);
  }

  .range-35 {
    @include responsive-offset(35px);
  }
  .range-30 {
    @include responsive-offset(30px);
  }

  .range-15 {
    @include responsive-offset(15px);
  }
  
  .range-10 {
    @include responsive-offset(10px);
  }

  @media (min-width: $screen-md-min) {
    .range-60 {
      @include responsive-offset(60px);
    }
    .range-55 {
      @include responsive-offset(55px);
    }
    .range-50 {
      @include responsive-offset(50px);
    }
    .range-md-30 {
      @include responsive-offset-media(30px);
    }
    
  }
  @media (min-width: $screen-lg-min) {
    .range-lg-0 {
      @include responsive-offset-media(0);
    }
  }
}