  }
}

.swiper-slide > .vide__body,
.swiper-slide > .parallax_cnt {
  height: 100%;
}


// Swiper navigation
//--------------------------------------------------

.swiper-button-prev,
.swiper-button-next {
  position: relative;
  transform: translate(-15px, calc(-100% - 20px));

  display: inline-block;

  font-size: 40px;
  line-height: 1;
  color: #fff;

  text-align: center;
  cursor: pointer;
  transition: .2s ease-in;
  will-change: transform;
  z-index: 10;

  @media (min-width: $screen-lg-min) {
    font-size: 60px;
  }

  &:hover {
    color: $orange;
  }

  @media (min-height: 650px) and (min-width: 1200px) {
    transform: translate(-15px, calc(-100% - 55px));
  }


  // Disabled button
  &.swiper-button-disabled{
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
}

.swiper-button-prev {

  &:before{
    font-family: "Material Design Icons";
    content: "\f237";
  }
}

.swiper-button-next {

  &:before{
    font-family: "Material Design Icons";
    content: "\f238";
  }
}


.swiper-slider.swiper-container-rtl{
  .swiper-button-prev {
    &:before{
      content: "\f061";
    }
  }

  .swiper-button-next {
    &:before{
      content: "\f060";
    }
  }
}



// Swiper pagination
//--------------------------------------------------

.swiper-pagination {
  position: absolute;
  display: block;
  text-align: center;
  transition: .3s;
  transform: translate3d(0, 0, 0);
  z-index: 10;

  &.swiper-pagination-hidden {
    opacity: 0;
  }

  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    display: inline-block;
    border-radius: 100%;
    background: rgba(255,255,255,.6);
    transition: .2s;

    &.swiper-pagination-bullet-active,
    &:hover{
      background: rgba(255,255,255,1);
    }
  }

  &.swiper-pagination-clickable .swiper-pagination-bullet {
    cursor: pointer;
  }
}

// Dark pagination
.swiper-pagination-black{
  .swiper-pagination-bullet {
    background: rgba(0,0,0,.6);

    &.swiper-pagination-bullet-active {
      background: rgba(0,0,0,1);
    }
  }
}


// Swiper pagination orientation
//--------------------------------------------------

// Vertical pagination
.swiper-container-vertical{
  > .swiper-pagination {
    right: 10px;
    top: 50%;
    transform: translate3d(0px, -50%, 0);

    .swiper-pagination-bullet {
      margin: 5px 0;
      display: block;
    }
  }
}


// Horizontal pagination
.swiper-container-horizontal{
  > .swiper-pagination {
    bottom: 20px;
    left: 0;
    width: 100%;

    .swiper-pagination-bullet {
      margin: 0 5px;
    }
  }
}


// Swiper slide styles
//--------------------------------------------------

.swiper-slide {
  position: relative;

  @include display-flex();
  @include align-items(center);
  @include justify-content(center);
  @include flex-shrink(0);
  width: 100%;
  height: 100%;

  white-space: nowrap;

  h1, .heading-1, h2, .heading-2, h3, .heading-3, h4, .heading-4, h5, .heading-5, h6, .heading-6, & {
    color: #fff;
  }

  &-caption {
    width: 100%;
    white-space: normal;
    
    [href*='callto'] {
      &:hover{
        color: $orange;
      }
    }

    h5,
    .heading-5 {
      font-family: $font-family-sec;
    }

    p {
      display: none;

      @media (min-width: $screen-sm-min) {
        display: block;
        margin-top: 15px;
      }
    }
    @media (min-width: $screen-lg-min) {
      p {
        margin-top: 30px;
      }
      * + .button {
        margin-top: 60px;
      }
    }
  }
}