
/**
* Box minimal
*/
.box-minimal {
  max-width: 365px;
  text-align: left;
  
  &_mod-1 {
    max-width: 100%;
  }

  * + p {
    margin-top: 4px;
  }

  .box-minimal-icon {
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    justify-content: center; 
    width: 60px;
    height: 60px;
    background-color: $primary;
    font-size: 30px;
    line-height: 1; 
    color: $white;
    transition: 300ms ease-in;
    
    &.icon-secondary {
      background-color: $secondary;
    }
  }
  
  &:hover{
    .box-minimal-icon {
      background-color: $secondary;
      &.icon-secondary {
        background-color: $primary;
      }
    }
  }
}

.box-minimal-icon {
  svg {
    fill: $primary;
  }
}

.box-minimal-title {
  color: $gray-darker;
  font-size: 20px;
  line-height: 1.2;
  font-family: $headings-font-family;
}


.box-minimal-header {
  @include display-flex;
  @include flex-direction(row);
  @include flex-wrap(nowrap);
  @include align-items(center);
  
  > * + * {
    margin-left: 15px;
  }
}

* + .box-minimal-text { margin-top: 22px; }

@media (min-width: $screen-lg-min) {
  .box-minimal {

    .box-minimal-icon {
      width: 72px;
      height: 72px; 
      font-size: 38px;
    }
  }
  .box-minimal-header {
    > * + * {
      margin-left: 25px;
    }
  }
  .box-minimal-title {
    font-size: 21px;
  }
}

@media (min-width: $screen-xl-min) {
  .box-minimal-title {
    font-size: 28px;
    line-height: 36px;
  }
}

/**
* Box inline
*/
.box-inline {
  margin-left: -7px;
  display: table;
  text-align: left;

  > * {
    display: table-cell;
    vertical-align: top;
    padding: 0 6px;
  }
  
  &-middle {
    > * {
      vertical-align: middle;
    }
  }

  .icon-md-smaller {
    position: relative;
    top: 3px;
  }
}

/**
* Box small
*/
.box-small {
  h3 + p {
    margin-top: 0;
  }
}

/**
* Call to action
 */

.cta-box,
.cta-box-small {
  margin-left: auto;
  margin-right: auto;
  padding: 25px 20px;
  background-color: $white;
  box-shadow: $shadow-area-sm;  
  text-align: center;
  
  &.cta-form {
    h4 {
      color: $gray-darker;  
    }
    * + form {
      margin-top: 25px;
    }
  }
}

.cta-box {max-width: 570px;}

.cta-box-small {
  * + .rd-mailform {
    margin-top: 20px;
  }
}

.cta-box-small {
  text-align: left;
}

.cta-box-icon {
  font-size: 60px;
}

.cta-box-title {
  font-family: $font-family-sec;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.2;
  color: $gray-darker;
}

@media (min-width: $screen-xs-min) {
  .cta-box {
    text-align: left;
  }
}

@media (min-width: $screen-sm-min) {
  .cta-box,
  .cta-box-small {
    padding: 30px 35px 25px;
  }
  .cta-box {
    font-size: 20px;
  }
  .cta-box-icon {
    font-size: 75px;
  }
  .cta-box-title {
    font-size: 36px;
  }
  .cta-box-small {
    h3 + p {
      margin-top: 20px;
    }
    * + .rd-mailform {
      margin-top: 20px;
    }
  }
}

@media (min-width: $screen-lg-min) {
  .cta-box {
    padding: 35px 48px 40px 52px;
    font-size: 22px;
    line-height: 30px;

    &.cta-form {
      padding: 50px 48px 65px 52px;
    }
  }
  .cta-box-small {
    padding: 40px 50px 50px 50px;
    max-width: 570px;
  }
  .cta-box-icon {
    font-size: 90px;
  }
  .cta-box-title {
    font-size: 42px;
    line-height: 52px;
  }
}


// box careers
.box-job-position {
  background-color: $white;
  padding: 15px;
  text-align: left;

  * + .list {
    margin-top: 15px;
  }
  
  &-inner {
    .icon {
      font-size: 22px;
    }
  }
  
  &-caption-overlay {
    padding-top: 25px;
    
    > * + * {
      margin-top: 10px;
    }
  }
}

.box-job-position-title {
  font-size: 22px;
  font-family: $font-family-sec;
  color: $primary;
}

@media (min-width: $screen-sm-min) {
  .box-job-position {
    * + .list {
      margin-top: 30px;
    }
  }
}

.desktop {
  @media (min-width: $screen-lg-min) {
    .box-job-position {
      position: relative;
      padding: 0;

      * + .list {
        margin-top: 44px;
      }
      
      &:hover{
        .box-job-position-caption-overlay {
          opacity: 1;
          pointer-events: auto;
        }
      }
    }
    .box-job-position-inner {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 30px 20px 25px 37px;
    }
    .box-job-position-caption-overlay {
      position: relative;
      z-index: 1;
      background-color: $secondary;
      padding: 30px 20px 25px 37px;
      transition: 260ms ease-in;
      
      opacity: 0;
      pointer-events: none;
      
      > * + * {
        margin-top: 15px;
      }
      
      * + .box-job-position-description {
        margin-top: 20px;
      }
      
      .box-job-position-title a,
      .box-job-position-description {
        color: $white;  
      }
      .box-job-position-title a:hover {
        color: $orange;
      }
      .icon-white:hover {
        color: $orange;
      }
    }
  }
}

// services box
.services-box {
  max-width: 470px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  
  &-icon {
    position: relative;
    width: 70px;
    height: 70px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 36px;
    background-color: $primary;
    color: $white;  
    border-radius: 50%;
    
     &:after{
       position: absolute;
       top: -10px;
       bottom: -10px;
       left: -10px;
       right: -10px;
       border-radius: 50%;
       content: '';
       background-color: $primary;
       opacity: .3;
       z-index: -1;
     }
  }
  &-title {
    * + & { margin-top: 25px; }
  }
  &-text {
    * + & { margin-top: 10px; }
  }
  .icon + h4 {
    margin-top: 15px;
  }
}

@media (min-width: $screen-lg-min) {
  .services-box {

    &-icon {
      width: 110px;
      height: 110px;
      font-size: 48px;
    }
    &-title {
      * + & { margin-top: 40px; }
    }
    &-text {
      * + & { margin-top: 20px; }
    }
  }
}