/*
*
* Text styling
* --------------------------------------------------
*/


// Text style
//

.text-italic {
  font-style: italic;
}

.text-normal {
  font-style: normal;
}

// Text decoration
//

.text-underline {
  text-decoration: underline;
}

.text-strike {
  text-decoration: line-through;
}

// Text weight
//

.text-thin {
  font-weight: 100;
}

.text-light {
  font-weight: 300;
}

.text-regular{
  font-weight: 400;
}

.text-medium{
  font-weight: 500;
}

.text-sbold{
  font-weight: 600;
}

.text-bold, strong {
  font-weight: 700;
}

.text-ubold {
  font-weight: 900;
}


// Text spacing
//

.text-spacing-0{
  letter-spacing: 0;
}

// Text family
//

.text-family-sec {
  font-family: $font-family-sec;
}

.text-family-base {
  font-family: $font-family-base;
}


@media (min-width: $screen-lg-min) {
  .text-width-lg-430 {
    max-width: 430px;
  }
}


@media (min-width: $screen-xl-min) {

  .offset-left-xl-45 {
    margin-left: 45px;
  }
}