// Footer style default
//------------------------------------

.page-footer-default {
  position: relative; 

  a {
    &.icon-gray-3:hover {
      color: $white;
    }
  }

  @media (min-width: $screen-sm-min) {

    * + .rights {
      margin-top: 110px;
    }
  }

  @media (min-width: $screen-md-min) {
    * + .rights {
      margin-top: 115px;
    }
  }

  @media (min-width: $screen-lg-min) {
    .rd-google-map-wrap {
      position: absolute;
      top: 0;
      right: 0;
      width: calc(100vw / 3);
    }
  }
}

.page-footer-default-var-2 {
  position: relative;
  .rd-mailform {
    .form-input {
      height: 50px;
      background: $blue-darker;
      color: $white;
      border-color: $blue-lighter;
    }
    .form-label {
      top: 25px;
      color: $white; 
    }
    textarea {
      min-height: 150px;
    }
  }
  >.range > [class*='cell-'] {
    position: relative;
    overflow: hidden;
  }
  @media (min-width: $screen-sm-min) {
    .rd-google-map-wrap {
      .rd-google-map__model {
        height: 350px;
      }
    }
  }
  @media (min-width: $screen-md-min) {
    .rights {
      margin-top: 130px;
    }
  }
  @media (min-width: $screen-lg-min) {
    .rd-google-map-wrap {
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100vw / 2.5);

      .rd-google-map__model {
        height: 560px;
      }
    }
    .form-footer {
      max-width: 320px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  @media (min-width: $screen-xl-min) {
    .rd-google-map-wrap {
      width: calc(100vw / 2);
    }
  }
}

// footer corporate
.page-footer-corporate {
  color: $gray-light;
  
  .rights {
    color: $text-color;
    padding-bottom: 30px;
  }
  
  .shell + .shell {
    margin-top: 0;
  }
  
  h4 + * {
    margin-top: 10px;
  }
  hr + * {
    margin-top: 15px;
  }
  
  .form-input {
    height: 50px;
  }
  .form-label {
    top: calc(50px / 2);
  }
  .gallery-sidebar {
    line-height: 0;
  }
  .gallery-sidebar .gallery-item {
    max-width: 100px;
    max-height: 70px;
    
    img {
      max-width: 100%;
    }
  }

  @media (min-width: $screen-md-min) {
    h4 + * {
      margin-top: 19px;
    }
    hr + * {
      margin-top: 25px;
    }
  }
}
