/*
* Navbar components
*/

// RD Navbar
.rd-navbar {
  display: none;
  background: $rd-navbar-background;
  font-family: $font-family-base;
}

// RD Navbar Toggle
.rd-navbar-toggle,
.rd-navbar-sidebar-toggle{
  @include make-toggle(
          $rd-navbar-toggle-preset,
          $rd-navbar-min-line-height,
          $rd-navbar-min-font-size,
          $rd-navbar-fixed-toggle-color
  );
  background-color: transparent;
  border: none;
  display: none;

  &:focus{
    outline: none;
  }
}

// RD Navbar Brand
.rd-navbar-brand {
  .brand-name {
    max-width: 175px;
    display: inline-block;
  }
  .logo-default {
    display: block;
  }
  .logo-inverse {
    display: none;
  }
}

// RD Navbar Dropdown
.rd-navbar-dropdown {
  display: none;
}

// Search toggle
.rd-navbar-search-toggle{
  display: inline-flex;
  color: $gray-darker;

  &:focus,
  &:hover {
    color: $rd-navbar-nav-color;
  }

  @include toggle-icons-via-rotation(25px, 25px, 22px, '\f43b', '\f24c', 'Material Design Icons', 'Material Design Icons');
}

.rd-navbar-search .form-input {
  background-color: $white;
  border-color: $white;
}