/*
*
* Quotes
* --------------------------------------------------
*/

/**
* Quote primary
*/
.quote-primary {
  text-align: left;

  .cite {
    color: $secondary;
  }
}

.quote-primary-text {
  font-style: italic;
  font-size: 18px;
  line-height: (28 / 18);
}

.quote-primary-mark {
  position: relative;
  top: 6px;
  display: block;
  width: 37px;
  height: 27px;
  fill: $primary;
  @include flex-shrink(0);
}

.quote-primary-meta {
  margin-left: -.2em;
  color: $gray-1;
  * {
    padding: 0 .2em;
  }
}

* + .quote-primary-text {
  margin-top: 20px;
}

* + .quote-primary-meta {
  margin-top: 15px;
}

@media (min-width: $screen-sm-min) {
  .quote-primary-text {
    font-size: 20px;
  }

  * + .quote-primary-text {
    margin-top: 30px;
  }
  * + .quote-primary-meta {
    margin-top: 20px;
  }
}

@media (min-width: $screen-lg-min) {
  .quote-primary-text {
    font-size: 24px;
    line-height: (34 / 24);
  }
}

/**
* Quote minimal
*/
.quote-minimal {
  padding-left: 30px;
  padding-right: 20px;
}

.quote-minimal-text {
  font-size: 18px;
  font-style: italic;
  color: $gray-darker;
}

@media (min-width: $screen-sm-min) {
  .quote-minimal {
    max-width: 90%;
  }
  .quote-minimal-text {
    font-size: 24px;
  }
}

/**
* Quote boxed
*/
.page {
  .quote-boxed {
    text-align: left;
    @include group(30px, 0);
  }
}


.quote-boxed-image {
  border-radius: 50%;
}

.quote-boxed-text,
.quote-boxed-cite {
  font-size: 18px;
}

.quote-boxed-text {
  position: relative;
  color: $gray-darker;

  &::before {
    position: relative;
    display: inline-block;
    transform: scale(-1, -1);
    top: -5px;
    left: -10px;
    content: '\f373';
    font-family: 'Material Design Icons';
    font-size: 45px;
    line-height: .8;
    color: $secondary;
  }
}

.quote-boxed-cite {
  font-weight: 400;
  color: $primary;
}

.quote-boxed-small {
  color: $gray-light;
}

.quote-boxed-meta {
  > * + * {
    margin-top: 0;
  }
}

* + .quote-boxed-meta {
  margin-top: 20px;
}

@media (max-width: $screen-xs-min - 1) {
  .quote-boxed-aside {
    text-align: left;

    > * {
      display: inline-block;

      + * {
        margin-left: 20px;
      }
    }
  }
}

@media (min-width: $screen-xs-min) {
  .quote-boxed {
    @include display-flex;
    @include flex-direction(row);
  }

  .quote-boxed-aside {
    @include flex-shrink(0);
    text-align: center;
  }

  .quote-boxed-main {
    @include flex-grow(1);
    transform: translateY(-13px);
  }
}

@media (min-width: $screen-sm-min) {

  .quote-boxed-text,
  .quote-boxed-cite {
    font-size: 20px;
    line-height: (34 / 24);
  }

  .quote-boxed-text {
    &::before {
      font-size: 60px;
      top: -9px;
    }
  }
}

@media (min-width: $screen-lg-min) {
  .quote-boxed {
    @include group(50px, 30px);
  }

  .quote-boxed-text {
    font-size: 22px;
  }
  .quote-boxed-cite {
    font-size: 24px;
  }
}

/**
* Quote vertical
*/
.quote-vertical {
}

.quote-vertical-image {
  width: 100px;
  height: auto;
  border-radius: 50%;
}

.quote-vertical-text {
  position: relative;
  color: $gray-darker;

  &::after {
    position: relative;
    display: inline-block;
    margin-top: 10px;
    content: '\f373';
    font-family: 'Material Design Icons';
    font-size: 40px;
    line-height: 1;
    color: $secondary;
  }
}

.quote-vertical-cite {
  font-weight: 400;
  color: $primary;
}

.quote-vertical-small {
  color: $gray-light;
}

.quote-vertical-meta {
  > * + * {
    margin-top: 0;
  }
}

* + .quote-vertical-text {
  margin-top: 15px;
}

* + .quote-vertical-meta {
  margin-top: 5px;
}

@media (min-width: $screen-xs-min) {
  .quote-vertical-text,
  .quote-vertical-cite {
    font-size: 18px;
    line-height: 1.5;
  }
}

@media (min-width: $screen-sm-min) {
  .quote-vertical-text,
  .quote-vertical-cite {
    font-size: 20px;
    line-height: (30 / 20);
  }

  .quote-vertical-text {
    &::after {
      margin-top: 20px;
    }
  }

  * + .quote-vertical-text {
    margin-top: 25px;
  }
}

@media (min-width: $screen-lg-min) {
  .quote-vertical-text,
  .quote-vertical-cite {
    font-size: 24px;
  }
}

/**
* Quote classic
*/
.quote-classic {
  text-align: left;
}

.quote-classic-image {
  width: 60px;
  height: auto;
  border-radius: 50%;
}

.quote-classic-header {
  @include display-flex;
  @include flex-direction(row);
  @include align-items(center);
  html & {
    @include group(20px, 20px);
  }
}

.quote-classic-text {
  position: relative;
  padding: 15px;
  font-style: italic;
  color: $white;
  background-color: $secondary;
  
  &:before{
    position: absolute;
    bottom: 100%;
    left: 15px;
    content: "";
    display: inline-block;
    
    @include triangle(top, 30px, 15px, $secondary)
  }
}

.quote-classic-cite {
  font-weight: 700;
  color: $primary;
}

.quote-classic-small {
  color: $gray-light;
}

.quote-classic-meta {
  > * + * {
    margin-top: 0;
  }
}

* + .quote-classic-text {
  margin-top: 20px;
}

@media (min-width: $screen-xs-min) {
  .quote-classic-text {
    font-size: 18px;
    line-height: 1.5;
  }
}

@media (min-width: $screen-sm-min) {
  .quote-classic-text {
    padding: 30px 30px 50px;
    font-size: 20px;
    line-height: (30 / 22);
    
     &:after{
       position: absolute;
       bottom: 2px;
       right: 15px;
       content: '\f373';
       font-family: 'Material Design Icons';
       font-size: 67px;
       line-height: 1;
       font-style: normal;
       color: $orange;
     }
  }
}

@media (min-width: $screen-xl-min) {
  .quote-classic-text {
    font-size: 22px;
  }
}

/**
* Quote modern
*/

.quote-modern {
  text-align: left;
}


.quote-modern-image {
  width: 60px;
  height: auto;
  border-radius: 50%;
}

.quote-modern-footer {
  @include display-flex;
  @include flex-direction(row);
  @include align-items(center);
  html & {
    @include group(20px, 20px);
  }
}

.quote-modern-text {
  position: relative;
  font-size: 18px;
  font-style: italic;

  &::before {
    position: relative;
    display: inline-block;
    transform: scale(-1, -1);
    left: -10px;
    content: '\f373';
    font-family: 'Material Design Icons';
    font-size: 60px;
    font-style: normal;
    line-height: 1;
    color: $primary;
  }
}

.quote-modern-cite {
  font-weight: 700;
  color: $primary;
}

.quote-modern-small {
  color: $gray-light;
}

.quote-modern-meta {
  > * + * {
    margin-top: 0;
  }
}

* + .quote-modern-text {
  margin-top: 20px;
}

* + .quote-modern-footer {
  margin-top: 20px;
}

@media (min-width: $screen-sm-min) {
  .quote-modern-cite {
    font-size: 16px;
  }
  .quote-modern-text {
    font-size: 20px;
    line-height: (30 / 22);
  }
}

@media (min-width: $screen-xl-min) {
  .quote-modern-cite {
    font-size: 16px;
  }
  .quote-modern-text {
    font-size: 22px;
  }
}