/*
* Static Layout
*/

.rd-navbar-static {
  display: block;

  .rd-navbar-inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .rd-navbar-nav {
    text-align: center;
    > li {
    }
  }

  .rd-navbar-aside-right{
    display: flex;
    align-items: center;
  }
  .rd-navbar-aside-right-inner {
    white-space: nowrap;
  }
  
  .rd-navbar-top-panel{
    text-align: left;
    padding: 15px 0;
  }

  .rd-navbar-nav{
    > li {
      margin: 0 13px;
      > a {
        padding: 35px 0 30px;
        
        &:before {
          position: absolute;
          top: 0;
          left: 0;
          right: 100%;
          height: 4px;
          content: "";
          display: inline-block;
          opacity: 0;
          transition: 250ms;
          background: $rd-navbar-nav-hover-color;
        }
        
        &:hover{
          &:before{
            right: 0;
            opacity: 1;
          }
        }
      }

      &.focus,
      &.opened{
        > a{
          &:before{
            right: 0;
            opacity: 1;
          }
        }
      }

      &.active {
        > a {
          &:before{
            right: 0;
            opacity: 1;
          }
        }
      }
    }
  }

  .rd-navbar-dropdown,
  .rd-navbar-megamenu {
    margin-top: 0;
  }

  .rd-navbar-megamenu {
    max-width: 1200px;
    > li + li {
      //margin-left: 85px;
    }
  }

  .rd-navbar-shop {
    .rd-navbar-shop-icon {
      font-size: 30px;

      span {
        position: relative;
        top: -3px;
        font-size: 18px;
        padding-left: 13px;
        vertical-align: middle;
      }
    }
  }

  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone{
    will-change: transform;
    .rd-navbar-nav{
      > li {
        > a {
          padding: 25px 0;
        }
      }
    }
     .rd-navbar-dropdown,
     .rd-navbar-megamenu {
       margin-top: 0;
     }
  }

  @media (min-width: $screen-xl-min) {
    .rd-navbar-nav {
      > li {
        margin: 0 20px;
        > a {
          font-size: 18px;
          padding: 55px 0;
        }
      }
    }
    .rd-navbar-dropdown,
    .rd-navbar-megamenu {
      margin-top: -25px;
      li{
        > a {
          font-size: 16px;
          line-height: 1.3;
        }
      }
    }
  }

  @media (min-width: $screen-xxl-min) {
    .rd-navbar-nav{
      > li {
        margin: 0 30px;
      }
    }
  }
}

