/*
*
* Buttons
* --------------------------------------------------
*/


// Button General Styles

.button {
  display: inline-block;
  position: relative;
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(13px, 28px, 15px, 20px, 0);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-style: solid;
  border-width: 0;  
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  user-select: none;
  transition: 250ms all ease-in-out;
  font-family: $font-family-sec;
  box-shadow: 0 4px 0 0;
  
  > span {
    position: relative;
    z-index: 1;
  }
  
   &:after {
     position: absolute;
     left: 20%;
     right: 20%;
     top: 0;
     bottom: 0;
     content: '';
     opacity: 0;
     pointer-events: none;
     transition: 250ms all ease-in-out;
   }

  @media (min-width: $screen-lg-min) {
    @include btn-size(16px, 43px, 18px, 20px, 0);
  }
  
  &:focus,
  &:active,
  &:active:focus,
  &:active:hover,
  &:focus:hover { 
    outline: none;
  }
  
  &:hover {
    //transform: translateY(0);
     &:after{
       left: 0;
       right: 0;
       opacity: 1;
     }
  }

  &.disabled {
    color: black; 
    background-color: gray;
  }

  .caret {
    margin-left: 8px;
  }
}

button.button, 
a.button {
  &:active {
    transform: translateY(4px);
    box-shadow: none;
  }
}

.button-block{
  display: block;
  width: 100%;
}


// Appearance style
// --------------------------------------------------

.button-default {
  @include btn-variant($gray-darker, $gray-lighter, $gray-lighter, $gray-darker, $secondary, $secondary);
  &:after{
    background-color: $secondary; 
  }
  &:hover{
    box-shadow: 0 4px 0 0 darken($secondary, 11.5%);
  }
}

.button-white {
  @include btn-variant($gray-darker, $white, $white, $white, $secondary, $secondary);
  box-shadow: 0 4px 0 0 $gray-4;  
  &:after{
    background-color: $secondary; 
  }
  &:hover{
    box-shadow: 0 4px 0 0 darken($secondary, 11.5%);
  }
}

.button-primary {
  @include btn-variant($white, $primary, $primary, $white, $secondary, $secondary);
  box-shadow: 0 4px 0 0 darken($primary, 11.5%);
  &:after{
    background-color: $secondary;
  }
  &:hover{
    box-shadow: 0 4px 0 0 darken($secondary, 11.5%);
  }
}

.button-secondary {
  @include btn-variant($white, $secondary, $secondary, $white, $primary, $primary);
  box-shadow: 0 4px 0 0 darken($secondary, 11.5%);
  &:after{
    background-color: $primary;
  }
  &:hover{
    box-shadow: 0 4px 0 0 darken($primary, 11.5%);
  }
}

// Social
// --------------
.button-facebook {
  @include btn-variant($white, $facebook, $facebook, $white, $facebook-darken, $facebook-darken);
}

.button-twitter {
  @include btn-variant($white, $twitter, $twitter, $white, $twitter-darken, $twitter-darken);
}

.button-google {
  @include btn-variant($white, $google, $google, $white, $google-darken, $google-darken);
}

.button-facebook-outline {
  @include btn-variant($facebook, transparent, $facebook, $white, $facebook, $facebook);
}

.button-twitter-outline {
  @include btn-variant($twitter, transparent, $twitter, $white, $twitter, $twitter);
}

.button-google-outline {
  @include btn-variant($google, transparent, $google, $white, $google, $google);
}


// Btn with shadow
.button-shadow{
  box-shadow: 0 3px 11px 0 rgba(0, 0, 0, 0.15);

  &:hover{
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.15);
  }
}


// Button Sizes
// --------------------------------------------------

.button-xs {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(11px, 28px, 14px, 18px, 0);
}

.button-sm {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(13px, 37px, 16px, 20px, 0);
}

.button-lg {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(18px, 58px, 20px, 22px, 0);
}

 
// Button Shapes
// --------------------------------------------------

.button-circle {
  border-radius: 30px;
}

.button-ellipse {
  &-md {
    border-radius: 5px;
  }

  &-lg {
    border-radius: 10px;
  }
}

html .page {
  .button-shadow-none {
    box-shadow: none;
  }
}

  // Button Icon styles
// --------------------------------------------------

.button {
  &.button-icon {
    //padding-left: 17px;
    //padding-right: 17px;

    .icon {
      font-size: 24px;
      line-height: inherit;
      vertical-align: middle;
      transition: 0s;
    }

    &-left {
      .icon {
        float: left;
        padding-right: 10px;
      }
    }

    &-right {
      .icon {
        float: right;
        padding-left: 10px;
      }
    }
  }
}

// Button tags
.button-tag {
  padding: 6px 13px;
  font: 400 12px/1 $font-family-sec;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: $white;
  background-color: $primary;
  transition: 300ms ease-in;

  &:focus,
  &:active,
  &:hover{
    color: $white;
    background-color: $secondary;
  }
}

// button link

.button-link {
  font-weight: 700;
  font-size: 16px;
  font-family: $font-family-base;
  color: $primary;
  
  &:hover{
    color: $secondary;     
  }
}

.button-link-secondary {
  color: $secondary;
  &:focus {
    color: $secondary;
  }
  &:hover{
    color: $primary;   
  }
}

.button-link-post {
  display: inline-block;
  * + & {
    margin-top: 35px;
  }
  .icon {
    padding-left: 10px;
  }
}