/*
*
* Icons
* --------------------------------------------------
*/


// Base styles
// --------------------------------------------------

.icon {
  display: inline-block;
  font-size: 26px;
  line-height: 1;

  &:before {
    position: relative;
    display: inline-block;
    font-weight: 400;
    font-style: normal;
    speak: none;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}


// Alternate icons
// --------------------------------------------------

.icon-default {

}

.icon-primary {
  color: $primary;
}

.icon-secondary {
  color: $secondary;  
}

.icon-orange {
  color: $orange;
}

.icon-white {
  color: $white;
}

.icon-gray-3 {
  color: $gray-3;  
}

.icon-gray-4 {
  color: $gray-4;  
}

.page {
  a {
    &.icon-gray-3 {
      color: $gray-3; 
      &:hover {
        color: $primary;
      }
    }
    &.icon-gray-light {
      color: $gray-light; 
      &:hover {
        color: $primary;
      }
    }
    
  }
}

// Social icon
.icon-facebook {
  color: $facebook;
}

.icon-twitter {
  color: $twitter;
}


// Icon Shapes
// --------------------------------------------------

.icon-circle{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  
  &.icon-secondary {
    background-color: $secondary;
    color: $white;
  }

  &.icon-primary {
    background-color: $primary;
    color: $white;
  }
  
  &.icon-gray-4 {
    background-color: $gray-4;
    color: $gray-darker;
  }
  

  &.icon-facebook,
  &.icon-twitter {
    color: $white;
  }

  &.icon-facebook {
    background-color: $facebook;
    &:hover{
      background-color: darken($facebook, 11.5%);
    }
  }

  &.icon-twitter {
    background-color: $twitter;
    &:hover{
      background-color: darken($twitter, 11.5%);
    }
  }
  
  &.icon-xs {
    width: 35px;
    height: 35px;
  }
  
  &.icon-sm {
    width: 60px;
    height: 60px;
  }
}


// Icon Sizes
// --------------------------------------------------

.page {
  
  .icon-xxs {
    font-size: 17px;
  }
  
  .icon-xs {
    font-size: 18px;
  }

  .icon-sm {
    font-size: 20px;
  }

  .icon-lg {
    font-size: 36px;
  }
  .icon-xl {
    font-size: 38px;
  }

  .icon-xxl {
    font-size: 45px;
  }
}