// RD navbar default

.rd-navbar-corporate {
  .rd-navbar {

  }
  .rd-navbar-nav-wrap-inner {
    display: none;
  }
 
  .rd-navbar-top-panel {
    .rd-navbar-top-panel-inner {
      max-width: 1200px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  .rd-navbar-corporate-container {
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;    
  }
  .rd-navbar-corporate-inner {
    display: flex;
    align-items: center;
    line-height: 1.4;
    text-align: left;
    width: 100%;
    
    .icon-circle.icon-sm {
      width: 40px;
      height: 40px;
    }
  }

  .box-inline {
    > * {
      display: inline-block;
      vertical-align: middle;
    }
    .icon {
      top: auto;
    }
  }
  
  .rd-navbar-panel {
    text-align: center;
  }

  &.rd-navbar-fixed {
    .rd-navbar-corporate-inner {
      display: none; 
    }
    .rd-navbar-nav-wrap-inner {
      display: block;
    }
    .rd-navbar-nav-wrap-inner {
      font-size: 14px;
      line-height: 1.6;
      padding-left: 16px;
      padding-right: 15px;
      .unit {
        text-align: left;
        + * {
          margin-top: 10px;
        }
      }
    }
    .icon-sm.icon-circle {
      width: 30px;
      height: 30px;
    }
    
    .rd-navbar-search {
      position: fixed;
      top: 16px;
      right: 55px;
    }
  }
  
  &.rd-navbar-static {
    .rd-navbar-panel {
      display: flex;
      align-items: center;
      justify-content: space-between;
      
      max-width: 1200px;
      padding-left: 15px;
      padding-right: 15px;
      margin-left: auto;
      margin-right: auto;
      
      padding-top: 43px;
      padding-bottom: 43px;
      
    }
    .rd-navbar-panel-nav {      
      background-color: $gray-lighter;
      
      &-inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        max-width: 1200px;
        padding-left: 15px;
        padding-right: 15px;
        margin-left: auto;
        margin-right: auto;
      }
    }
    .rd-navbar-nav {
      position: relative;
      > li {
        margin: 0;
        > a {
          font-size: 16px;
          padding: 20px 33px;
          //vertical-align: middle;
          &:before{
            content: none;
          }
          &:hover{
            color: $rd-navbar-nav-color;
            background: $secondary;
          }
        }
        .rd-navbar-submenu-toggle {
          right: 30px;
        }
        &.focus,
        &.opened{
          > a {
            color: $rd-navbar-nav-color;
            background: $secondary; 
          }

          > .rd-navbar-submenu-toggle{
            color: $rd-navbar-nav-color;
          }
        }

        &.active {
          > a {
            color: $rd-navbar-nav-color;
            background: $secondary;
          }

          > .rd-navbar-submenu-toggle{
            color: $rd-navbar-nav-color;
          }
        }

        > .rd-navbar-dropdown {
          left: 0;
        }
      }
    }
    .rd-navbar-megamenu,
    .rd-navbar-dropdown {
      margin-top: 0;
    }
    .rd-navbar-megamenu {
      width: 100%;
    }
    .rd-navbar-search .rd-search {
      top: calc(100% + 15px);
    }
    
    &.rd-navbar--is-stuck {
      .rd-navbar-panel {
        display: none;
      }
    }
  }

  @media (min-width: $screen-xxl-min) {
    .rd-navbar-static {

      .rd-navbar-inner {
        
      }
    }
  }
}
