// Gallery Item
//
.gallery-item {
  position: relative;
  @include display-flex;
  @include flex-direction(column);
  @include align-items(center);
  @include justify-content(center); 
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  overflow: hidden;
  width: 100.1%;
  perspective: 1000px;

  > * {
    width: 100%;
  }

  figure {
    width: 100%;
    transition: .55s all ease-in-out;
  }

  img {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: auto;
    height: auto;
    max-width: none;
    min-width: 101.5%;
  }

  .caption {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    @include display-flex;
    @include flex-direction(column);
    @include justify-content(center);
    @include align-items(center);
    padding: 20px;
    transition: .33s all ease-in-out;
    pointer-events: none;
    background: rgba($gray-base, .7);

    > * {
      pointer-events: auto;
      position: relative;
      z-index: 2;
    }

    &:before {
      content: '\f508';
      font-family: "Material Design Icons";
      font-size: 42px;
      color: #fff;

      .ie-10 &,
      .ie-11 &,
      .ie-edge & {
        line-height: .6;
      }
    }
  }

  .caption-title,
  .caption-text {
    color: $white;
  }

  .caption-title {
    position: relative;
    font-size: 24px;
    line-height: 1.45;
    font-family: $font-family-sec;
    color: $secondary;  
  }

  .caption-text {
    max-width: 100%;
    margin-top: 15px;
    padding: 0 15px;
    color: $gray-4;
  }
}

.desktop {
  @media (min-width: $screen-md-min) {
    //gallery item
    .gallery-item {
      .caption {
        transition: .4s all ease;
        opacity: 0;

        &:before {
          transform: scale3d(0.7, 0.7, 0);
          transition: 380ms 50ms transform;
        }
      }

      .caption-title,
      .caption-text {
        transition: 300ms 50ms ease-in-out;
        transform: scale3d(0.7, 0.7, 0);
      }

      .caption-text { max-width: 340px; }

      &:hover {
        figure { transform: scale3d(1.05, 1.05, 1.05); }

        .caption {
          opacity: 1;
          &:before { transform: scale3d(1, 1, 1); }
        }
        .caption-title,
        .caption-text {
          transform: scale3d(1, 1, 1);
        }
      }
    }
  }
}


// Gallery sidebar
.gallery-sidebar {
  * + & {
    margin-top: 30px;
  }
  @media (min-width: $screen-xs-min) {
    .gallery-item {
      max-width: calc(50% - 10px);
      max-height: 120px;

      img {
        max-width: 100%;
      }
    }
  }
}