/*
*
* Lists
* --------------------------------------------------
*/


ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}


.list {
  > li + li {
    margin-top: 10px;
  }
}

// list sizes
.list-xxs {
  > li + li { margin-top: 5px; }
}
.list-xs {
  > li + li { margin-top: 17px; }
}

.list-sm {
  > li + li { margin-top: 20px; }
}

.list-lg {
  > li + li { margin-top: 25px; }
}

.list-xl {
  > li + li { margin-top: 35px; }
}

@media (min-width: $screen-sm-min) {
  .list-lg {
    > li + li { margin-top: 30px; }
  }

  .list-xl {
    > li + li { margin-top: 45px; }
  }
}

@media (min-width: $screen-lg-min) {
  .list-sm {
    > li + li { margin-top: 20px; }
  }

  .list-lg {
    > li + li { margin-top: 40px; }
  }

  .list-xl {
    > li + li { margin-top: 60px; }
  }
}

@media (min-width: $screen-xl-min) {
  .list-lg {
    > li + li { margin-top: 60px; }
  }
  .list-xl {
    > li + li { margin-top: 90px; }
  }
}


// List inline
//

.list-inline {
  margin-left: -5px;
  margin-right: -5px;

  > li {
    padding-left: 5px;
    padding-right: 5px;
  }
}

.list-inline-sm { 
  margin-left: -9px;
  margin-right: -9px;

  > li {
    padding-left: 9px;
    padding-right: 9px;
  }
}


// Description Lists
//

dl {
  margin: 0;
}


// List terms
//

.list-terms {
  dt + dd { margin-top: 10px; }
  dd + dt { margin-top: 25px; }

  @media (min-width: $screen-md-min) {
    dt + dd { margin-top: 20px; }
    dd + dt { margin-top: 60px; }
  }
}


// List index
//

.list-index {
  counter-reset: li;

  li {
    .list-index-counter {
      &:before {
        content: counter(li, decimal)". ";
        counter-increment: li;
      }
    }
  }
}


/**
* List marked
*/
.list-marked {
  padding-left: 25px;
  text-align: left;

  > li {
    position: relative;
    padding-left: 25px;

    &:before {
      position: absolute;
      top: 4px;
      left: 0;
      content: '\f238';
      font-family: 'Material Design Icons';
      font-size: 18px;
      font-weight: 400;
      line-height: 1;
      color: $gray-1;
    }
  }

  a {
    //color: $gray-darker;

    &:hover {
      //color: $primary;
    }
  }

  > li + li {
    margin-top: 10px;
  }
}

.list-marked-checked {
  padding-left: 0;

  > li {
    padding-left: 40px;

    &:before {
      content: '\f222';
      font-family: 'Material Design Icons';
      font-size: 24px;
      line-height: .6;
      color: $primary;
      vertical-align: middle;
    }
  }
}

/**
* List ordered
*/
.list-ordered {
  padding-left: 25px;
  counter-reset: li;
  text-align: left;

  li {
    position: relative;
    padding-left: 25px;

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      display: inline-block;
      width: 15px;
      content: counter(li, decimal) '.';
      counter-increment: li;
      color: $gray-darker;
    }
  }

  li + li {
    margin-top: 10px;
  }
}

/**
* List comma
*/
.list-comma {
  > li {
    display: inline-block;
  }

  > li:not(:last-child) {
    &:after {
      content: ',';
      display: inline-block;
      margin-right: 3px;
    }
  }
}


/**
* List contact
*/
.list-contact {
  h4 {
    & + * {
      margin-top: 10px;
    }
    @media (min-width: $screen-lg-min) {
      font-size: 24px;

      & + * {
        margin-top: 20px;
      }
    }
  }
  
  a {
    color: $gray-3; 
    &:hover{
      color: $primary;   
    }
  }
}

.contact-aside {
  @media (min-width: $screen-xs-min) and (max-width: $screen-sm-max) {
    > li {
      display: inline-block; 
      width: 48.5%;
      padding-right: 1.4%;
      vertical-align: top;
      &:nth-child(2) {
        margin-top: 0;
      }
    }
  }
}

// list 2 column
.list-2-column {
  > li {
    -webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    break-inside: avoid;
  }
  @media (min-width: $screen-sm-min) {
    -moz-columns: 2;
    columns: 2;
    -moz-column-gap: 30px;
    column-gap: 30px;
  }
  @media (min-width: $screen-xl-min) {
    -moz-column-gap: 65px;
    column-gap: 65px;
  }
}

// list location
@media (min-width: $screen-lg-min) {
  .list-location {
    padding-right: 25px;
  }
}

.inset-left-0 {
  padding-left: 0;
}