/*
*
* Thumbnails
* --------------------------------------------------
*/


.thumbnail-classic-title {
  font-family: $headings-font-family;
  font-size: 22px;
  color: $primary;

  &:hover{
    color: $secondary;
  }
  
  &.heading-4 {
    font-size: 22px; 
  }
}
.thumbnail-classic-caption {
  margin-top: 15px;
}

.thumbnail-classic-image {
  display: inline-block;
  transition: 300ms ease-in;
  will-change: transform;
  &:hover{
    box-shadow: 4px 4px 0 rgba($gray-base, .1);
    transform: translateY(-4px); 
  }
}

.thumbnail-classic-hover {
  .thumbnail-classic-image {
    position: relative;
    &:before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba($primary,.8);
      content: '';
      transition: 300ms ease-in;
      opacity: 0;
    }
    &:after {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: '\f508';
      font-family: "Material Design Icons";
      font-size: 52px;
      color: $white;
      transition: 300ms ease-in;
      opacity: 0;
    }
    &:hover {
      &:before,
      &:after {
        opacity: 1;
      }
      box-shadow: none;
      transform: translateY(0);
    }
  }
}

@media (min-width: $screen-sm-min) {
  .thumbnail-classic {
    text-align: left;
  }
}

@media (min-width: $screen-md-min) {
  .thumbnail-classic-title {
    font-size: 28px;
  }
}

// Thumb
//

.thumb{
  position: relative;
  display: inline-block;

  // Bg overlay
  &:before{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    background-color: rgba($primary, .4);
    z-index: 1;
    content: '';
    will-change: opacity;
  }

  // Icon
  &:after{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    color: $white;
    font-family: FontAwesome;
    font-size: 34px;

    content: "\f065";
    z-index: 1;
    will-change: transform, opacity;
  }

  &:before,
  &:after{
    transition: .3s ease;
    opacity: 0;
  }

  &:hover{
    &:before,
    &:after{
      opacity: 1;
    }
  }
}