// RD navbar default

.rd-navbar-default {
  
  .button {
    transform: translateY(-2px);
    
    &:active{
     transform: translateY(2px);   
    }
  }
  
  .rd-navbar {
    
  }
  
  .rd-navbar-fixed {
    .rd-navbar-collapse {
      flex-wrap: wrap;
      justify-content: flex-start;
    }
    .rd-navbar-aside-right {
      display: none;
    }
  }

  .rd-navbar-fullwidth,
  .rd-navbar-static {
    .rd-navbar-nav-wrap-inner {
      display: none;
    }
  }
  
  .rd-navbar-fullwidth {
    .rd-navbar-aside-right-inner {
      margin-top: 0;
      
      > * + * {
        margin-left: 30px;
      }
    }
    .rd-navbar-inner {
      display: flex;
      flex-wrap: wrap;
      vertical-align: center;

      > * {
        flex-grow: 1;
      }

      .rd-navbar-panel {
        width: 100%;
        margin-bottom: 20px;
      }
    }
  }

  .rd-navbar-static {
    .rd-navbar-aside-right-inner {
      > * + * {
        margin-left: 30px;
      }
    }
    @media (min-width: $screen-xxl-min) {
      .rd-navbar-aside-right-inner {
        > * + * {
          margin-left: 50px;
        }
      }
    }
  }

  .rd-navbar-static,
  .rd-navbar-fullwidth {

    // RD Navbar Nav
    .rd-navbar-nav {
      > li {

        > a {
          color: $gray-darker;
          
          &:hover {
            color: $rd-navbar-nav-hover-color;

            & ~ .rd-navbar-submenu-toggle {
              color: $rd-navbar-nav-hover-color;
            }
          }
        }

        > .rd-navbar-submenu-toggle {
          color: $gray-darker;
        }

        &.focus,
        &.opened{
          > a {

            color: $rd-navbar-nav-hover-color;

            & ~ .rd-navbar-submenu-toggle {
              color: $rd-navbar-nav-hover-color;
            }
          }

        }

        &.active {
          > a {
            color: $rd-navbar-nav-hover-color;

            & ~ .rd-navbar-submenu-toggle {
              color: $rd-navbar-nav-hover-color;
            }
          }
        }
      }
    }

    .rd-navbar-search-toggle {
      color: $gray-darker;

      &:focus,
      &:hover {
        color: $rd-navbar-nav-hover-color;
      }
    }
  }
}