/*
*
* Form styles
* --------------------------------------------------
*/

//== Forms variables
//

$input-height: 60px;

$form-wrap-offset: 15px;

$textarea-default-height:       135px;
$textarea-default-min-height:   $textarea-default-height * 0.8;
$textarea-default-max-height:   $textarea-default-height * 1.7;

$input-color: $gray-3;
$input-color-placeholder: $gray-light;

$input-bg: $gray-lighter;

$input-border: $gray-4;
$input-border-radius: 0;

$input-font-size: 16px;
$input-line-height: 22px;
$input-font-weight: 400;

$input-padding-vertical: 12px;
$input-padding-horizontal: 18px;

$error-color: #d9534f;
$success-color: #5cb85c;



.rd-mailform {
  position: relative;
  text-align: left;
}

html .rd-mailform-inline{
  @include display-flex();

  .form-wrap{
    @include flex-grow(1);
  }

  .button{
    margin-top: 0;
    flex: none;
    box-shadow: none;
    &:active {
      transform: none;
    }
  }
  
  .form-input,
  .form-label {
    color: $secondary-1;  
  }
}

.recaptcha{
  transform: scale(0.83);
  transform-origin: left top;
}


// Form input
// -------------------------

.form-input {
  display: block;
  width: 100%;
  height: $input-height;
  padding: $input-padding-vertical $input-padding-horizontal;
  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  background-color: $input-bg;
  background-image: none;
  border: 1px solid $input-border;
  border-radius: $input-border-radius;
  -webkit-appearance: none;

  &:focus {
    outline: 0;
  }

  &:-webkit-autofill ~ label,
  &:-webkit-autofill ~ .form-validation {
    color: $gray-base !important;
  }
}

textarea.form-input {
  height: $textarea-default-height;
  min-height: $textarea-default-min-height;
  max-height: $textarea-default-max-height;
  resize: vertical;
}

// Form wrapper
// -------------------------

.form-wrap {
  position: relative;
}

.form-wrap + .form-wrap{
  margin-top: $form-wrap-offset;
}

* + .form-button,
* + .form-button-wrap,
.form-wrap + .button{
  margin-top: 30px;
}

.form-button-wrap {
  display: flex;
  justify-content: space-between;
  
  > * {
    width: auto; 
  }
  ul {
    margin-top: 0;
  }
}


// Form labels
// -------------------------

.form-label {
  position: absolute;
  top: $input-height / 2;
  left: $input-padding-horizontal;

  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  pointer-events: none;
  z-index: 9;
  transition: .3s;
  transform: translateY(-50%);

  &.focus {
    opacity: 0;
  }

  &.auto-fill {
    color: $input-color;
  }
}

.form-label-outside {
  color: $input-color-placeholder;
  @media (min-width: $screen-sm-min) {
    position: static;
    &, &.focus, &.auto-fill {
      opacity: 1;
      transform: none;
      color: $input-color-placeholder;
      font-size: $input-font-size;
    }

    & + .form-input + .form-validation{
      top: 32px;
    }
  }
}


// Form validation
// -------------------------

.form-validation {
  position: absolute;
  right: 10px;
  top: 3px;
  font-size: 11px;
  line-height: 11px;
  color: $error-color;
  margin-top: 3px;
  transition: .3s;
  z-index: 11;
}

.form-validation-left {
  .form-validation {
    right: auto;
    top: 0;
    left: 10px;
  }
}


// Form output
// -------------------------

#form-output-global {
  position: fixed;
  bottom: 0;
  visibility: hidden;
  transform: translateY(calc(100% + 30px));
  transition: .3s all ease;
  z-index: 9999999;
  margin: 15px;

  &.active {
    transform: translateY(0);
    visibility: visible;
  }

  @media (min-width: $screen-sm) {
    margin: 30px;
  }
}

.form-output {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 2px;
  transition: .3s;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &.error {
    color: $error-color;
  }

  &.success {
    color: $success-color;
  }
}


// Radio and Checkbox Custom
// -------------------------


// Base Styles
//

.radio .radio-custom,
.radio-inline .radio-custom,
.checkbox .checkbox-custom,
.checkbox-inline .checkbox-custom {
  opacity: 0;

  &, &-dummy {
    position: absolute;
    width: 14px;
    height: 14px;
    margin-left: -20px;
    margin-top: 5px;
    outline: none;
    cursor: pointer;
  }

  &-dummy {
    pointer-events: none;
    background: $gray-lighter;
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.15);

    &:after {
      position: absolute;
      opacity: 0;
    }
  }

  &:focus {
    outline: none;
  }
}

.radio-custom:checked + .radio-custom-dummy:after,
.checkbox-custom:checked + .checkbox-custom-dummy:after {
  opacity: 1;
}


// Custom Radio Styles
//

.radio,
.radio-inline {
  .radio-custom-dummy {
    border-radius: 50%;

    &:after {
      content: '';
      top: 3px;
      right: 3px;
      bottom: 3px;
      left: 3px;
      background: $gray-darker;
      border-radius: 50%;
    }
  }
}


// Custom Checkbox Styles
//

.checkbox,
.checkbox-inline {
  padding-left: 20px;
  .checkbox-custom-dummy {
    pointer-events: none;
    border-radius: 3px;
    margin-left: 0;
    left: 0;

    &:after {
      content: '\f222';
      font-family: "Material Design Icons";
      font-size: 20px;
      line-height: 10px;
      position: absolute;
      top: 0;
      left: 0;
      color: $gray-darker;
    }
  }
}

// Custom toggle
.toggle-custom-dummy {
  content: "";
  display: inline-block;
  width: 32px;
  height: 14px;
  background-color: rgb(119, 119, 119);
  border-radius: 15px;
  transition: background 0.3s ease;
  vertical-align: middle;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.15) 1px 1px 1px 0px inset;
}
.toggle-custom-dummy::after {
  content: "";
  display: inline-block;
  width: 20px;
  height: 21px;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.25) 1px 1px 3px 0px;
  left: -9px;
  top: -4px;
  transition: left 0.3s ease, background 0.3s ease, box-shadow 0.1s ease;
}

.toggle-custom:checked + .toggle-custom-dummy {
  background-color: $primary;
}

.toggle-custom:checked + .toggle-custom-dummy::after {
  left: 19px;
}

.toggle-custom {
  display: none;
}

.form-search {
  position: relative;

  .form-input {
    //border: 1px solid $gray-6;
    padding-right: 60px;
    color: #4c2121;
    background-color: $input-bg;
    border-color: $input-border;
  }

  .form-label {
    color: $secondary-1;
  }
  
  * + .button {
    margin-top: 0;
  } 
  
  * + & {
    margin-top: 34px;
  }
}


.form-search-submit {
  position: absolute;
  font-size: 25px;
  right: 20px;
  top: 0;
  bottom: 0;
  padding: 0;
  background: none;
  border: none;
  box-shadow: none;
  color: $gray-darker;
  transition: .3s all ease;
  &:hover, &:focus {
    color: $primary;
    outline: none;
  }
}

.cta-form {
  .button {
    width: 100%;
  }

  .form-input {
    height: 50px;
  }
}

.form-quote {
  padding-top: 65px;
  padding-bottom: 95px;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 510px;
  margin-left: auto;
  margin-right: auto;
  
  .form-input,
  .select2-container .select2-choice{
    height: 50px;
  }
  .form-label {
    top: 25px;
  }
  
  .range {
    margin-left: -5px;
    margin-right: -5px;
    
    > [class*='cell-'] {
      padding-left: 5px;
      padding-right: 5px;
    }
  }
}