// RD navbar default

.rd-navbar-with-top-panel {
  .rd-navbar {

  }
  .rd-navbar-nav-wrap-inner {
    display: none;
  }
 
  .rd-navbar-top-panel {
    .rd-navbar-top-panel-inner {
      
    }
  }
  
  .rd-navbar-with-top-panel-inner {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    line-height: 1.4;
    text-align: left;
    
    .icon-circle.icon-sm {
      width: 30px;
      height: 30px;
      @media (min-width: $screen-xl-min) {
        width: 40px;
        height: 40px;
      }
    }
  }

  .box-inline {
    > * {
      display: inline-block;
      vertical-align: middle;
    }
    .icon {
      top: auto;
    }
  }
  
  .rd-navbar-panel {
    text-align: center;
  }


  &.rd-navbar-fixed {

    .rd-navbar-panel {
      &:before {
        z-index: 999;
      }
    }
    .rd-navbar-toggle,
    .rd-navbar-brand {
      z-index: 999;
    }

    .rd-navbar-with-top-panel-inner {
      > .rd-navbar-panel-nav-inner {
        display: none;
      }
    }
    
    .rd-navbar-nav-wrap-inner {
      display: block;
      .button-sm {
        font-size: 14px;
        width: 100%;
      }
    }
    .rd-navbar-nav-wrap-inner {
      font-size: 14px;
      line-height: 1.6;
      padding-left: 16px;
      padding-right: 15px;
      .unit {
        text-align: left;
        + * {
          margin-top: 10px;
        }
      }
    }
    .icon-sm.icon-circle {
      width: 30px;
      height: 30px;
    }
    
    .rd-navbar-search {
      position: fixed;
      top: 16px;
      right: 15px;
    }
  }
  
  &.rd-navbar-static {
    .rd-navbar-inner {
      
    }
    .rd-navbar-brand {
      flex: 0 0 auto;
      padding-right: 6%;
      @media (min-width: $screen-xl-min) {
        .brand-name {
          max-width: 269px;
        }
      }
    }
    .rd-navbar-panel {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0;
      @media (min-width: $screen-xl-min) {
        padding: 0;
      }
    }
    .rd-navbar-panel-nav {
      position: relative;
      flex-grow: 1;
      width: 100%;
      margin-top: 25px;
      background-color: $secondary;
      @media (min-width: $screen-xl-min) {
        margin-top: 40px;
      }
      
      &:before {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 100vw;
        content: "";
        background-color: $primary;
      }
      .rd-navbar-panel-nav-inner {
        margin-top: 0;
      }
      
      &-inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        width: 100%;
        margin-top: 13px;
        @media (min-width: $screen-xl-min) {
          margin-top: 38px;
        }

        .left-side {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          margin-left: -1.4em;
          margin-right: -1.4em;
          
          > * {
            padding: 10px 1.4em;
            font-size: 14px;
          }
          .unit {
            flex: 0 1 auto;
          }
          @media (min-width: $screen-xl-min) {
            margin-left: -1.9em;
            margin-right: -1.9em;
            > * {
              padding: 10px 1.9em;
              font-size: 16px;
            }
          }
        }
        .right-side {
          .button {
            font-size: 14px;
          }
          @media (min-width: $screen-xl-min) {
            .button {
              font-size: 16px;
            }
          }
        }
      }
    }
    .rd-navbar-nav {
      position: relative;
      letter-spacing: 0;
      > li {
        margin: 0 18px;
        > a {
          font-size: 16px;
          padding: 22px 0;
          &:before{
            //content: none;
            background: $white;
          }
          &:hover{
            color: $rd-navbar-nav-color;
            background: transparent;
          }
        }
        .rd-navbar-submenu-toggle {

        }
        &.focus,
        &.opened{
          > a {
            color: $rd-navbar-nav-color;
            background: transparent;
          }

          > .rd-navbar-submenu-toggle{
            color: $rd-navbar-nav-color;
          }
        }

        &.active {
          > a {
            color: $rd-navbar-nav-color;
            background: transparent;
          }

          > .rd-navbar-submenu-toggle{
            color: $rd-navbar-nav-color;
          }
        }
        
      }
      @media (min-width: $screen-xl-min) {
        > li {
          margin: 0 33px;
          .rd-navbar-submenu-toggle {
            
          }
        }
      }
    }
    .rd-navbar-megamenu,
    .rd-navbar-dropdown {
      margin-top: 0;
    }
    .rd-navbar-megamenu {
      width: 100%;
    }
    .rd-navbar-search .rd-search {
      top: calc(100% + 15px);
    }
    
    &.rd-navbar--is-stuck {
      .rd-navbar-with-top-panel-inner {
        flex-grow: 1;
        > .rd-navbar-panel-nav-inner {
          display: none; 
        }
      }
      .rd-navbar-panel {
        padding: 0;
        width: 100%;
      }
      .rd-navbar-panel-nav {
        margin-top: 0;
      }
      .rd-navbar-brand .brand-name {
        max-width: 168px;
      }
    }
  }

  @media (min-width: $screen-xxl-min) {
    .rd-navbar-static {

      .rd-navbar-inner {
        
      }
    }
  }
}
