/**
* 2.5  Breadcrumbs
*/
$breadcrumb-padding-vertical: 8px;
$breadcrumb-padding-horizontal: 15px;
$breadcrumb-color: $white;
$breadcrumb-color-path: $orange;
$breadcrumb-active-color: $white;
$breadcrumb-separator: "\f238";

.breadcrumbs-custom { 
  position: relative;
  z-index: 1;
  padding: 35px 0 40px;
  vertical-align: middle;
  color: $breadcrumb-color;
  background-image: url(../images/breadcrumbs-background-01.jpg);
  background-repeat: no-repeat;
  background-size: cover;

  > .shell {
    position: relative;
    z-index: 2;
  }
  p {
    margin: 0;
  }
}

.breadcrumbs-custom-wrap {
  position: relative;
}

.breadcrumbs-custom-path {
  a {
    display: inline;
    vertical-align: baseline;
    &, &:active, &:focus {
      color: $breadcrumb-color-path;
    }
    
    &:hover{
      color: $white;
    }
  }

  li {
    position: relative;
    display: inline-block;
    vertical-align: baseline;
    margin-right: 32px;
    font-size: 16px;
    line-height: 1.7;

    &:after {
      content: $breadcrumb-separator;
      font-family: 'Material Design Icons';
      position: absolute;
      top: 53%;
      right: -25px;
      display: inline-block;
      font-style: normal;
      color: $breadcrumb-color;
      transform: translateY(-50%);
      font-size: 20px;
    }
  }

  li:last-child {
    margin-right: 0;
    &:after { display: none; }
  }
  
  li.active {
    color: $breadcrumb-active-color;
  }
}

.breadcrumbs-custom-title {
  position: relative;
  display: inline-block;
  color: $breadcrumb-color;
}

.breadcrumbs-custom-subtitle {
  font-size: 20px;
  line-height: 1.3;
  font-family: $headings-font-family;
  color: $breadcrumb-color;
}

* + .breadcrumbs-custom-title { margin-top: 12px; }
* + .breadcrumbs-custom-path { margin-top: 20px; }

// Breadcrumbs light
.breadcrumbs-custom-light {
  @extend %context-dark;

  .breadcrumbs-custom-path {
    a {
      display: inline;
      vertical-align: baseline;
      &, &:active, &:focus {
        color: $white;
      }

      &:hover {
        color: $secondary-1;
      }
    }
    li.active {
      color: $white;
    }
  }
}

// breadcrumbs background
.breadcrumbs-background-01 {
  background-image: url(../images/breadcrumbs-background-01.jpg);
  background-size:cover; 
}

// Breadcrumbs blog post
.breadcrumbs-blog-post {
  padding-bottom: 20px;
  background-image: url(../images/breadcrumbs-background-02.jpg);
  background-size:cover;
  .breadcrumbs-custom-title {
    max-width: 930px;
  }
  > *,
  .breadcrumbs-custom-title,
  .breadcrumbs-custom-path li::after,
  .breadcrumbs-custom-path li.active {
    color: #fff;
  }

  &:before{
    content: none;
  }
  
  .box-inline {
    margin-left: auto;
    margin-right: auto;
  }
  * + .breadcrumbs-custom-title { margin-top: 15px; }
}

@media (max-width: $screen-xs-max) {
  
}

@media (min-width: $screen-xs-min) {
  .breadcrumbs-custom-path {
    > li {
      margin-right: 52px;

      &:after { right: -35px; }
    }
  }
}

@media (min-width: $screen-sm-min) {
  .breadcrumbs-custom {
    padding: 80px 0 30px;
  }

  .breadcrumbs-custom-subtitle {
    font-size: 24px;
  }

  * + .breadcrumbs-custom-path { margin-top: 50px; }

  .breadcrumbs-blog-post {
    padding-bottom: 40px;
  }
}

@media (min-width: $screen-lg-min) {
  .breadcrumbs-custom {
    padding: 110px 0 30px;
  }

  .breadcrumbs-custom-path li {
    font-size: 16px;
  }
}

@media (min-width: $screen-xl-min) {
  .breadcrumbs-blog-post {
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: space-between;
    
    * + .breadcrumbs-custom-path { margin-top: 145px; }
    * + .breadcrumbs-custom-title { margin-top: 55px; }
  }
  
  * + .breadcrumbs-custom-path { margin-top: 88px; }
}