/*
*
* Pagination custom
* --------------------------------------------------
*/

//== Pagination variables
//

$pagination-padding-vertical: 10px;
$pagination-padding-horizontal: 10px;

$pagination-font-size: 14px;
$pagination-lh-size: 20px;
$pagination-border-w: 1px;

$pagination-color:                     $white;
$pagination-bg:                        $secondary;
$pagination-border:                    #cdcdcd;

$pagination-hover-color:               $white;
$pagination-hover-bg:                  $primary;
$pagination-hover-border:              $primary;

$pagination-active-color:              $gray-darker;
$pagination-active-bg:                 $gray-4;
$pagination-active-border:             $gray-4;

$pagination-disabled-color:            $gray-darker;
$pagination-disabled-bg:               $gray-4;
$pagination-disabled-border:           $gray-4; 


//
// Pagination
// --------------------------------------------------
$pagination-size: 52px;
$pagination-offset: 20px;

.page {
  .pagination-custom {
    @include spacing(10px);
  }
}
.pagination-custom {
  position: relative;
  line-height: 0;
  font-size: 0;

  li {
    display: inline-block;
    vertical-align: middle;
  }

  li a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    min-height: $pagination-size;
    min-width: $pagination-size;
    height: $pagination-size;
    padding: 10px 20px;
    border-radius: 0;
    font: 400 16px/24px $font-family-sec;
    vertical-align: middle;
    box-shadow: inset 0 -4px 0 0 darken($secondary, 11.5%);

    &:after,
    &:before {
      font: 400 20px/1 'Material Design Icons';
    }

    &,
    &:active,
    &:focus {
      color: $pagination-color;
      background: $pagination-bg;
    }

    &:hover {
      color: $pagination-hover-color;
      background: $pagination-hover-bg;
      box-shadow: inset 0 -4px 0 0 darken($primary, 11.5%);
    }
  }

  li:first-child,
  li:last-child {
    a {
      padding-left: 12px;
      padding-right: 12px;
    }
  }

  li:first-child a {
    &:before {
      content: '\f14a';
    }
  }

  li:last-child a {
    &:before {
      content: '\f14f';
    }
  }

  li.disabled,
  li.active {
    pointer-events: none;
    box-shadow: inset 0 -4px 0 0 rgba($gray-base, .15); 
  }

  li.active a {
    color: $pagination-active-color;
    background: $pagination-active-bg;
    box-shadow: inset 0 -4px 0 0 rgba($gray-base, .15);
  }

  li.disabled a {
    color: $pagination-disabled-color;
    background: $pagination-disabled-bg;
    box-shadow: inset 0 -4px 0 0 rgba($gray-base, .15);
  }
}


* + .pagination-custom {
  margin-top: 30px;
}

@media (min-width: $screen-sm-min) {
  * + .pagination-custom {
    margin-top: 40px;
  }
}