/*
*
* Owl Carousel
* --------------------------------------------------
*/
* + .owl-carousel {
  margin-top: 35px;
}
* + .owl-carousel-inset {
  margin-top: 20px;
}

@media (min-width: $screen-sm-min) {
  .owl-carousel-posts + .owl-carousel-posts {
    margin-top: 0;
  }
}
@media (min-width: $screen-lg-min) {
  * + .owl-carousel {
    margin-top: 60px;
  }
  * + .owl-carousel-inset {
    margin-top: 45px;
  }
}

.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* 
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  transition: height 500ms ease-in-out;
}

/* 
 *  Core Owl Carousel CSS File
 */
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;
}

.owl-carousel-inset {
  .owl-stage-outer {
    padding-top: 15px;
    padding-bottom: 15px; 
  }
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}

.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
  font-size: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot {
  cursor: pointer;
  user-select: none;
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}

.owl-carousel.owl-hidden {
  opacity: 0;
}

.owl-carousel .owl-refresh .owl-item {
  display: none;
}

.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  will-change: transform;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

.owl-carousel .owl-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.owl-carousel.owl-rtl {
  direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
  float: right;
}

/* No Js */
.no-js .owl-carousel {
  display: block;
}

/* 
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease;
}

/* 
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  font: 400 40px/80px 'FontAwesome';
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  transition: scale 100ms ease;
  &:before {
    content: '\f144';
  }
}

.owl-carousel .owl-video-play-icon:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
}

/*
 * Owl Navigation
 */

.owl-nav {
  &.disabled {
    display: none;
  }
}

.owl-prev, .owl-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font: 400 40px/50px 'Material Design Icons';
  color: $primary;
  cursor: pointer;
  
  &:hover {
    color: $secondary;
  }
  &.disabled {
    cursor: default;
    pointer-events: none;
    color: rgba($gray-light, 1)
  }
}

.owl-prev {
  left: 10px;

  &:before {
    content: '\f237';
  }
}

.owl-next {
  right: 10px;

  &:before {
    content: '\f238';
  }
}

/*
 * Owl Pagination
 */

.owl-dots {
  text-align: center;
  margin-top: 30px;

  @media (min-width: $screen-lg-min) {
    margin-top: 60px;
  }
  
  &.disabled {
    display: none;
  }
}

.owl-dot {
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  position: relative;
  margin-left: 10px;
  width: 16px;
  height: 16px;
  outline: none;
  cursor: pointer;
  background-color: transparent;

  @media (min-width: $screen-xl-min) {
    margin-left: 30px;
  }

  span {
    border: 2px solid $primary;
    transition: border-color 0.3s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    outline: none;
    border-radius: 50%;
    background-color: transparent;
    cursor: pointer;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      visibility: hidden;
      background: $primary;
      border-radius: 50%;
      box-shadow: 0 0 1px $primary;
      opacity: 0;
      transform: scale(3);
      -moz-transition: opacity 0.3s ease, -moz-transform 0.3s ease, visibility 0s 0.3s;
      -webkit-transition: opacity 0.3s ease, -webkit-transform 0.3s ease, visibility 0s 0.3s;
      transition: opacity 0.3s ease, transform 0.3s ease, visibility 0s 0.3s;
    }
  }

  &.active,
  &:hover,
  &:focus {
    span {
      border-color: $primary;
    }
  }

  &.active,
  &:hover {
    span {
      &:after {
        visibility: visible;
        opacity: 1;
        transform: scale(1);
        -moz-transition: opacity 0.3s ease, -moz-transform 0.3s ease;
        -webkit-transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
        transition: opacity 0.3s ease, transform 0.3s ease;
      }
    }
  }
}

.owl-carousel.owl-carousel-light-dots{
  .owl-dot{
    border-color: $white;

    &:hover,
    &:focus {
      background-color: $white;
    }

    &.active {
      background-color: $white;
    }
  }
}

// Team
.owl-carousel-team {
  .owl-dots {
    padding-bottom: 25px;
  }
}

// Posts
.owl-carousel-posts {
  .owl-stage,
  .owl-item {
    display: flex;
  }
  .owl-prev {
    left: -5px;
  }
  .owl-next {
    right: -5px;
  }
}



// testimonials
.carousel-testimonials { 
  max-width: 645px;
  
  .owl-prev,
  .owl-next {
    top: 3%;
    transform: none;
    width: 35px;
  }

  .owl-prev {
    left: auto;
    right: 25px;
  }
  .owl-next {
    right: -10px;
  }

  .quote-classic-header {
    padding-right: 50px;
  }
}

.carousel-testimonials-var-2 {
  * + & {
    margin-top: 10px;
  }
  .owl-prev,
  .owl-next {
    top: auto;
    bottom: 3%;
    transform: none;
    width: 35px;
  }
  .owl-prev {
    left: auto;
    right: 25px;
  }
  .owl-next {
    right: -10px;
  }
} 