// =======
// Team
// =======

/**
* team inline
*/
.box-team-inline {
  background-color: $white;
  max-width: 270px;
  margin-left: auto;
  margin-right: auto;
  transition: 350ms ease-in;

  [href*='callto:'] {
    color: $gray-darker;
    &:hover {
      color: $primary;
    }
  }

  &:hover {
    box-shadow: $shadow-area-md;
  }

  &-title {
    display: inline-block;
    font-family: $headings-font-family;
    font-size: 22px;
  }

  &-subtitle {
    font-style: italic;
    color: $gray-light;
    display: block;
  }
  &-body {
    padding-bottom: 25px;
    padding-left: 15px;
    padding-right: 15px;

    > * + * {
      margin-top: 10px;
    }
    .box-team-inline-subtitle {
      margin-top: 0;
    }
  }
  &-image {
    display: block;
  }
  .unit__left {
    position: relative;
  }
  .list-inline {
    position: absolute;
    bottom: 10px;
    width: 100%;
    margin: 0;
    text-align: center;
    z-index: 1;
  }
  .icon-gray-3.icon-circle {
    background: $white;

    &:hover {
      background: $secondary;
      color: $white;
    }
  }
  @media (max-width: 479px) {
    .box-inline {
      display: inline-block;
    }
  }
}

@media (min-width: $screen-xs-min) {
  .box-team-inline {
    max-width: 100%;
    text-align: left;

    &-body {
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 0;
      padding-right: 15px;
      > * + * {
        margin-top: 20px;
      }
    }

    &-image {
      max-width: 180px;
      overflow: hidden;

      img {
        position: relative;
        max-width: none;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    &-title {

    }
  }
}

@media (min-width: $screen-sm-min) {
  .box-team-inline {
    max-width: 650px;
    &-image {
      left: auto;
      transform: none;
    }
  }
}

@media (min-width: $screen-lg-min) {
  .box-team-inline {

    &-image {
      max-width: 100%;
      img {
        width: 110%;
      }
    }
    .list-inline {
      opacity: 0;
      transition: 350ms;
    }
    &:hover {
      .list-inline {
        opacity: 1;
      }
    }
  }
}

@media (min-width: $screen-xxl-min) {
  .box-team-inline {

    &-image {
      img {
        width: 100%;
      }
    }
  }
}


// box team default
.box-team-default {
  max-width: 270px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  
  &-caption {
    margin-top: 17px;
  }
  &-subtitle {
    display: block;
  }
  a,
  .box-team-inline-title {
    display: inline-block;
    overflow: hidden;
  }
  a {
    img {
      transition: 350ms;
    }
   &:hover {
     img {
       transform: scale3d(1.05,1.05,1.05);
     }
   }
  }
}

// box team large 
.box-team-large {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  &-caption {
    position: relative; 
    &-header {
      padding: 15px 20px;
      text-align: left;
      background-color: $secondary;
      > * {
        color: $white;
      }
      a:hover {
        color: $orange;
      }
      .box-team-inline-subtitle {
        opacity: .7;
      }
    }
    &-button {
      display: block;
      width: 100%; 
      padding: 15px;
      font-family: $font-family-sec;
      background-color: $orange;
      color: $gray-darker;
      box-shadow: inset 0 -4px 0 0 darken($orange, 11.5%);
      
      &:hover{
        background-color: $primary;
        color: $white;
        box-shadow: inset 0 -4px 0 0 darken($primary, 11.5%);
      }
    }
  }
}
.box-team-large-image {
  width: 100%;
}

@media (min-width: $screen-lg-min) {
  .box-team-large {
    max-width: 100%;
    &-caption {
      &-button {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;;
      }
      &-header {
        position: relative;
        z-index:1;
        padding: 23px 25px;
        transition: 350ms ease-out;
      }
    }
    &:hover{
      .box-team-large-caption-header {
        transform: translateY(-52px);
      }
    }
  }
}