/*
*
* Posts
* --------------------------------------------------
*/

.post-blog {

  &__title {

  }
}
.post-title {
  display: inline-block;
  font-size: 20px;
  line-height: 1.28;
  color: $secondary;
}

// post classic
.post-classic {
  * + & {
    margin-top: 30px;
  }
}
.post-classic-image {
  position: relative;

  .list-tags {
    position: absolute;
    top: 20px;
    left: 30px;
  }
}
.post-classic-body {
  padding: 20px 15px 25px;
  background-color: $white;
  
  &-footer {
    * + & { margin-top: 15px; }
    > * + * { margin-top: 15px; }
  }
}
.post-classic-aside {
  margin-left: auto;
  margin-right: auto;
  text-align: center;

  time {
    display: block;
    margin-left: auto; 
    margin-right: auto;
    color: $gray-light; 
  }
}
.post-classic-main {
  position: relative;
  overflow: hidden;
  text-align: left;
  margin-top: 15px;
}
.post-classic-avatar {
  display: block;
  border-radius: 100%;
  margin-left: auto;
  margin-right: auto;
}
.post-classic-author {
  font-size: 18px;
}


// Post minimal 
.post-minimal {
  & + & {
    margin-top: 45px;
  }
  @media (min-width: $screen-sm-min) {
    & + & {
      margin-top: 25px;
    }
  }
}
.post-minimal-title {
  font-size: 18px;
  font-weight: 700;
}
.post-minimal-horizontal {
  html & {
    @include group(15px);
  }
  * + .post-meta-bottom {
    margin-top: 3px;
  }
  * + .list-tags {
    margin-top: 10px;
  }
  
  @media (min-width: $screen-xs-min) {
    display: flex;
  }
  @media (min-width: $screen-sm-min) {
    
    html & {
      @include group(25px);
    }
  }
  @media (min-width: $screen-lg-min) {
    .post-minimal-title {
      font-size: 22px;
    } 
  }
}

.post-minimal-image {
  min-width: 150px;
}

// post modern 
.post-modern {
  width: 100%;
  max-width: 270px;
  margin-left: auto;
  margin-right: auto;
  
  &-body {
    padding: 20px 15px 25px;
    background-color: $white;
    
    &.post-modern-body-gray {
      background: $gray-lighter;
      &:before{
        border-color: transparent transparent $gray-lighter transparent;
      }
    }
  }
  &-image { overflow: hidden; }
}

.bg-white {
  .post-modern {

    &-body {

      &.post-modern-body-gray {
        background: $white;
        &:before{
          border-color: transparent transparent $white transparent;
        }
      }
    }
  }
}

[class*='post-arrow-'] {
  position: relative;
  &:before {
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    display: inline-block;
    @include triangle(top, 30px, 15px, $white);
    z-index: 1;
  }
}


// post modern vertical
.post-modern-vertical {

  width: 100%;
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $screen-sm-min) {
    display: flex;
    flex-direction: column;
    min-height: 100%;
  }

  &-sm-reverse {
    @media (min-width: $screen-sm-min) {
      flex-flow: column wrap;
      flex-direction: column-reverse;
    }
  }
  &-image {
    position: relative;
    width: 100%;
    img{
      width: 100%;
    }
    .icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  .icon {
    font-size: 34px;
    color: $gray-darker;  
    &:hover{
      background: $secondary;
      color: $white;
    }
  }
  
  @media (min-width: $screen-sm-min) {
    max-width: 100%;

    &-image {
      .icon {
        opacity: 0;
        transform: translate(-50%, -50%) rotate(-25deg);
      }
    }
    &:hover{
      .post-modern-vertical-image {
        .icon {
          opacity: 1;
          transform: translate(-50%, -50%) rotate(0deg);
        }
      }
      .post-modern-vertical-body {
        background: $secondary;
        * { color: $white; }
        a:hover { color: $orange;  }
      }
      [class*='post-arrow-']:before { border-color: transparent transparent $secondary transparent; }
      .post-arrow-bottom:before {
        border-color: $secondary transparent transparent transparent;
      }
    }
  }
}
.post-modern-vertical-body {
  transition: 270ms ease-in;;
  padding: 30px 15px 60px;
  text-align: center;
  p {
    display: block;
  }
  *,
  &:before {
    transition: 270ms ease-in;; 
  }
}
.post-modern-vertical-title {
  font-family: $headings-font-family;
  font-size: 22px;
  & + * {
    margin-top: 8px;
  }
}

@media (min-width: $screen-sm-min) {
  .post-modern-vertical-body {
    flex-grow: 1;
    padding: 30px 15px 30px;
  }
  .post-arrow-bottom {
    &:before {
      top: 100%;
      bottom: auto;
      @include triangle(bottom, 30px, 15px, $white);
    }
  }
}

.post-meta-bottom {
  margin-left: -18px;
  margin-right: -18px;
  * {
    color: $gray-light;
  }
  a:hover {
    color: $primary;
  }

  > li {
    position: relative;
    padding: 5px 18px;
    display: inline-block;

    &:before {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      content: "";
      display: inline-block;
      border-left: 1px solid;
      height: 24px;
    }
    &:last-of-type {
      &:before {
        content: none;
      }
    }
  }
}

//single post
.single-post-body {
  & + * {
    margin-top: 20px;
  }
  * + & {
    margin-top: 15px;
  }
  * + .list-inline {
    margin-top: 15px;
  }
  
  & + & {
    margin-top: 15px; 
  }
  
  .heading-5 {
    color: $gray-darker;  
  }
  .quote-modern {
    padding-left: 20px;
  }
  html & {
    @include group(15px);
  }
  
  &-inner {
    color: $gray-darker;
  }
  &-aside {
    color: $gray-light;
  }
}
.single-post-title {
  font-weight: 400;
  color: $primary;
}

* + .post-title { margin-top: 15px; }
* + .post-meta-bottom { margin-top: 15px; }

@media (min-width: $screen-xs-min) {
  // post modern
  .post-modern {
    max-width: 370px;
  }
}

@media (min-width: $screen-sm-min) {
  .post-title { font-size: 24px; }

  //post masonry
  .post-masonry {
    * + ul.post-meta-bottom {
      margin-top: 30px;
    }
  }
  
  //  post classic
  .post-classic {
    * + & {
      margin-top: 40px;
    }
  }
  .post-classic-body {
    padding: 25px;
  }
  .post-classic-body-footer {
    display: flex;
    justify-content: space-between;
    align-items:center;
    
    * + & { margin-top: 30px; }
    > * + * { margin-top: 0; }
  }
  .post-classic-container {
    display: flex;
    align-items: flex-start;
    padding-bottom: 40px;
    overflow: hidden;
    
    & + & {
      margin-top: 0;
    }
  }
  .post-classic-aside {
    position: relative;
    padding-bottom: 20px;
    width: 20%;
    &::after {
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      height: 100vh;
      border-right: 1px solid $gray-4;
    }
  }
  .post-classic:last-of-type {
    .post-classic-aside:after {
      content: none;
    }
  }
  .post-classic-avatar-wrap {
    position: relative;
    img { position: relative; }
    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 400px;
      border-bottom: 1px solid $gray-4;
    }
  }
  .post-classic-main {
    width: 80%;
    padding-left: 50px;
    margin-top: 0;
  }

  // post modern
  .post-modern {
    display: flex;
    max-width: 680px;
    text-align: left;
    > * {
      flex-grow: 1;
      width: 100%;
      max-width: 50%;
    }
    &-image-right {
      flex-direction: row-reverse;
    }
    &-image {
      position: relative;
      img {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        max-width: none;
        width: auto;
        height: auto;
      }
    }
    &-body {
      display: flex;
      flex-direction: column;
      padding: 25px 20px 25px 25px;

      > .post-modern-body-inner { padding-bottom: 15px; }
      > .post-meta-bottom { margin-top: auto; }
    }
  }
  * + .post-title { margin-top: 20px; }

  .post-arrow-left {
    &:before {
      position: absolute;
      bottom: 50%;
      left: auto;
      right: 100%;
      transform: translateY(50%);
      content: "";
      display: inline-block;
      @include triangle(left, 15px, 30px, $white);
    }
  }
  .post-arrow-right {
    &:before {
      position: absolute;
      bottom: 50%;
      left: 100%;
      transform: translateY(50%);
      content: "";
      display: inline-block;
      @include triangle(right, 15px, 30px, $white);
    }
  }
  .post-modern-body-gray {
    &.post-arrow-left:before {
      border-color: transparent $gray-lighter transparent transparent;
    }
    &.post-arrow-right:before {
      border-color: transparent transparent transparent $gray-lighter;
    }
  }

  .bg-white {
    .post-modern-body-gray {
      &.post-arrow-left:before {
        border-color: transparent $white transparent transparent;
      }
      &.post-arrow-right:before {
        border-color: transparent transparent transparent $white;
      }
    }
  }

  //  single post
  .single-post-body {
    display: flex;
    & + * {
      margin-top: 40px;
    }
    * + & {
      margin-top: 30px;
    }
    
    &-inner {
      flex: 0 0 75%;
      max-width: 870px;
      padding-right: 20px;
    }
    &-aside {
      max-width: 270px;
    }
  }
}

@media (min-width: $screen-md-min) {
  //post classic
  .post-classic-body {
    padding: 30px 40px 30px 37px;
  }
  .post-classic-author {
    font-size: 22px;
  }
  
  // post grid 
  .post-grid {
    display: flex;
    flex-direction: column;
    min-height: 750px;
    .post-classic-body {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      
      p {
        margin-bottom: 15px;
      }

      ul.post-meta-bottom {
        margin-top: auto;
      }
    }
    
    .ie-10 &,
    .ie-11 &,
    .ie-edge & {
      flex-flow: row wrap;
      .post-classic-image {
        flex: 0 0 auto; 
      }
      .post-classic-image,
      .post-classic-body {
        width: 100%;
        flex-flow: row wrap;
      }
    }
  }
  
  //post masonry
  .post-masonry {
    * + ul.post-meta-bottom {
      margin-top: 40px;
    } 
  }

  // post modern
  .post-modern {
    max-width: 900px;
  }

  .single-post-body {
    & + & {
      margin-top: 20px;
    }
    * + .quote-modern {
      margin-top: 15px;
    }
    .quote-modern + * {
      margin-top: 30px;
    }
    * + .list-inline {
      margin-top: 30px;
    }
    .quote-modern {
      padding-left: 60px;
    }
  }
  
}

@media (min-width: $screen-lg-min) {
  .post-modern {
    max-width: 960px;
    &-image {
      img {
        position: absolute;
        top: 0;
        height: 100%;
      }
    }
  }
  //  single post
  .single-post-body {

    &-inner {
      padding-right: 60px;
    }
    &-aside {
      padding-right: 30px;
    }
  }
}

@media (min-width: $screen-xl-min) {
  .post-title { font-size: 28px; }
  
  .post-modern {
    &-align-right { margin-right: 0; }
    &-align-left { margin-left: 0; }
    &-image img { position: relative; }
    &-body { padding: 40px 45px 40px 35px; }
  }
  
  //post masonry
  .post-masonry {
    * + ul.post-meta-bottom {
      margin-top: 60px;
    }
  }
  
  //post classic
  .post-classic-main {
    padding-left: 84px;
  }
}

@media (min-width: $screen-xxl-min) {

}

// Blog aside 

.blog-aside {
  > * + * {
    margin-top: 30px;
  }
  h4 + * {
    margin-top: 30px;
  }
  * + .post-meta-bottom {
    margin-top: 5px;
  }
  
  &-item {
    padding: 25px;
    background-color: $white;
  }
  .list-marked {
    padding-left: 0;
  }
}

@media (min-width: $screen-sm-min) {
  .blog-aside {
    > * + * {
      margin-top: 40px;
    }
    * + .list-marked {
      margin-top: 21px;
    }
    
    &-item {
      padding: 33px 40px 40px;
    }
  }
}